            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">1. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">בחוק זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;ועדה רפואית&quot; ו&quot;ועדה רפואית עליונה&quot; &ndash; ועדה כאמור שהורכבה מכוח סעיף 51;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;זמן&quot;, לענין חובת התייצבות לפי חוק זה &ndash; לרבות תקופת זמן שתחילתה מיום מסויים או מעשיית דבר מסויים או מאירוע מאורע מסויים;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חוקי השיקום&quot; &ndash; חוק הנכים (תגמולים ושיקום), תשי&quot;ט-1959 [נוסח משולב], חוק משפחות חיילים שנספו במערכה (תגמולים ושיקום), תש&quot;י-1950, חוק החיילים המשוחררים (החזרה לעבודה), תש&quot;ט-1949, וכל חוק אחר ששר הבטחון יכריז עליו כעל חוק שיקום;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חוק שירות המילואים&quot; &ndash; חוק שירות המילואים, התשס&quot;ח-2008;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חייל מילואים&quot; &ndash; כהגדרתו בחוק שירות המילואים;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;יוצא-צבא&quot; &ndash; אזרח ישראלי או תושב קבוע שמלאו לו שמונה עשרה שנים וטרם קיבל פטור משירות ביטחון מחמת גיל לפי סעיף 36א;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;מוסמך&quot; &ndash; (נמחקה);</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008 (תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;מיועד לשירות בטחון&quot; &ndash; אזרח ישראלי או תושב קבוע שמלאו לו שש עשרה שנים וחצי וטרם קיבל פטור משירות ביטחון מחמת גיל לפי סעיף 36א, ואשר עדיין לא התייצב לשירות ביטחון;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;פוקד&quot; &ndash; מי שמונה לפוקד לפי סעיף 50;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 9) &nbsp;תשנ&quot;ח-1997</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;פקודות הצבא&quot; &ndash; הוראות הפיקוד העליון, פקודות המטה הכללי ופקודות כלליות אחרות, כמשמעותן בחוק השיפוט הצבאי, תשט&quot;ו-1955;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;רופא&quot; &ndash; מי שהוא מורשה או זכאי להיות מורשה לעסוק ברפואה לפי פקודת הרופאים [נוסח חדש], תשל&quot;ז-1976;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;רופא שיניים&quot; &ndash; מי שהוא מורשה או זכאי להיות מורשה לעסוק ברפואת שיניים לפי פקודת רופאי השיניים [נוסח חדש], תשל&quot;ט-1979;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שירות בטחון&quot; &ndash; שירות סדיר או שירות מילואים;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שירות סדיר&quot; &ndash; שירות בכוחות הסדירים של צבא-הגנה לישראל;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שירות מילואים&quot; &ndash; שירות בכוחות המילואים של צבא-הגנה לישראל לפי חוק שירות המילואים;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תושב קבוע&quot; &ndash; אדם שמקום מגוריו הקבוע הוא בשטח שחל עליו משפט מדינת ישראל, או מי שרואים את שהותו כישיבת קבע מכוח סעיף 45.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif2' id='seif2'>חישוב גילים [2]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">2. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">לענין חוק זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;חישוב גילים ייעשה לפי הלוח העברי;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;(נמחקה);</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2א) &nbsp; &nbsp; &nbsp; &nbsp;(נמחקה);</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp;בדבר קביעת הגיל יחולו ההוראות שבתוספת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name='med2' id='med2'>פרק ב&apos;: התייצבות לרישום ולבדיקה</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif3' id='seif3'>התייצבות לרישום [3]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">3. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;פוקד רשאי, בצו, לקרוא לכל מיועד לשירות בטחון או יוצא-צבא להתייצב לרישום במקום ובזמן שהפוקד או מי שהוא הסמיך לכך קבע בצו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מי שנקרא להתייצב כאמור חייב להתייצב במקום ובזמן שנקבעו כאמור ולמסור לפוקד או לאדם שמינה הפוקד לכך את הפרטים הנוגעים אליו שנקבעו בתקנות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מי שנקרא להתייצב כאמור ומילא את המוטל עליו לפי סעיף קטן (ב), וחל שינוי בפרט מן הפרטים הנזכרים בו שנקבע בתקנות כפרט יסודי, חייב להודיע לפוקד על השינוי תוך ארבעה עשר ימים מיום השינוי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מיועד לשירות בטחון או יוצא-צבא שהתייצבו לרישום מכוח סעיף זה, חייבים לשאת אתם תמיד תעודה המעידה על התייצבותם, כפי שנקבע בתקנות, ולהראותה לשוטר וכן לפוקד או למי שהוא הסמיך, כשיידרשו לכך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">4. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif5' id='seif5'>התייצבות לבדיקת כושר [4] (תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">5. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;פוקד רשאי, בצו, לקרוא לכל יוצא-צבא או מיועד לשירות בטחון להתייצב לבדיקה במקום ובזמן שנקבעו בצו, לשם קביעת כשרו לשירות בטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;יוצא-צבא או מיועד לשירות בטחון שנקרא להתייצב כאמור, חייב להתייצב, במקום ובזמן שקבע בצו הפוקד או מי שהוא הסמיך לכך ולהיבדק כל בדיקה שתידרש לדעת ועדה רפואית, לשם קביעת כשרו הרפואי לשירות בטחון, וכן להיבדק, לפי הוראות הפוקד או מי שהוא הסמיך לכך, כל בדיקה אחרת לשם קביעת כשרו הכללי בשירות בטחון, לרבות בדיקה לשם קביעת מידת התאמתו למילוי תפקיד פלוני בשירות בטחון; המבחנים לכושר רפואי לדרגותיו ייקבעו בתקנות.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;text-indent:-51.05pt;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; (1) &nbsp; סיימה ועדה רפואית את בדיקתו של יוצא צבא או מיועד לשירות ביטחון תקבע הועדה, בהתאם למבחנים לכושר שנקבעו לפי סעיף קטן (ב), אם היא מוצאת אותו כשר לשירות ביטחון (להלן &ndash; כשר לשירות), בלתי כשר לשירות ביטחון (להלן &ndash; בלתי כשר לשירות) או בלתי כשר ארעית לשירות ביטחון (להלן &ndash; בלתי כשר ארעית לשירות);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מצא פוקד כי יוצא צבא או מיועד לשירות ביטחון, שנמצא בלתי כשר לשירות או בלתי כשר ארעית לשירות, מתאים לשרת בתפקיד צבאי בתנאי שירות מיוחדים, רשאית הועדה הרפואית, בהתאם למבחנים לכושר שנקבעו לפי סעיף קטן (ב), למצוא את יוצא הצבא או את המיועד לשירות ביטחון כשר לשירות ביטחון בתנאים מיוחדים (להלן &ndash; כשר לשירות ביטחון בתנאים מיוחדים);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; מי שנמצא כשר לשירות בתנאים מיוחדים, דינו לענין חוק זה כדין מי שנמצא כשר לשירות;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; לענין חוק זה, &quot;תנאי שירות מיוחדים&quot; &ndash; תנאי שירות הקבועים בפקודות הצבא, המתאימים לכושרו הרפואי של המיועד לשירות ביטחון או של יוצא הצבא, בתפקידים ובמקצועות הנדרשים על פי צורכי הצבא.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif5a' id='seif5a'>ערר על החלטת ועדה רפואית (תיקון מס&apos; 16) &nbsp;תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">5</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; &nbsp; יוצא צבא או מועמד לשירות ביטחון הרואה את עצמו נפגע מהחלטת ועדה רפואית, וכן פוקד, רשאים להגיש ערר על החלטת הוועדה הרפואית לפני ועדה רפואית עליונה, בהתאם לתנאים שנקבעו בתקנות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif5b' id='seif5b'>ערעור על החלטת ועדה רפואית עליונה (תיקון מס&apos; 16) &nbsp;תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">5</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; &nbsp; (א) &nbsp;יוצא צבא, מועמד לשירות ביטחון או פוקד רשאים לערער על החלטת ועדה רפואית עליונה לפני בית המשפט לעניינים מינהליים בתל-אביב-יפו, בשאלה משפטית בלבד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;שר המשפטים רשאי לקבוע, בצו, בהסכמת נשיא בית המשפט העליון, בתי משפט לעניינים מינהליים נוספים אשר יהיו מוסמכים לדון בערעורים כאמור בסעיף זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר המשפטים, בהתייעצות עם השר, רשאי לקבוע, בתקנות, סדרי דין, מועדים ודרך להגשת ערעור כאמור בסעיף זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif6' id='seif6'>בדיקה חוזרת [5]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">6. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;יוצא-צבא שנמצא בלתי כשר לשירות או בלתי כשר ארעית לשירות, רשאי פוקד לקראו בצו להתייצבות לבדיקה חוזרת לשם קביעת כשרו לשירות (להלן &ndash; בדיקה חוזרת), והוא חייב להתייצב במקום ובזמן שקבע בצו הפוקד או מי שהוא הסמיך לכך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הסמכות הנתונה לפוקד לפי סעיף קטן (א) נתונה לו גם לגבי יוצא-צבא ששירת שירות סדיר, או היה נמנה עם כוחות המילואים, וניתן לו פטור משירות סדיר או משירות מילואים מחמת שנמצא בלתי כשר לשירות או בלתי כשר ארעית לשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מי שנמצא בלתי כשר לשירות ובבדיקה חוזרת נמצא כשר לשירות, הרי אם טרם נקרא לשירות סדיר, או אם נקרא לשירות סדיר אך טרם השלים את תקופת השירות שהוא חייב בה לפי חוק זה, רשאי פוקד בצו לקרוא לו &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; להתייצב לשירות סדיר או להשלמת תקופת השירות &ndash; אם טרם מלאו לו עשרים ושלוש שנים;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; להתייצב לשירות סדיר או להשלמת תקופת השירות &ndash; לתקופה שלא תעלה על ששה חדשים או על תקופה שיש בה כדי להשלים את תקופת השירות שהוא חייב בה לפי חוק זה, לפי התקופה הקצרה יותר &ndash; אם מלאו לו עשרים ושלוש שנים;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; להתייצב לשירות סדיר או להשלמת תקופת השירות &ndash; לתקופה כאמור בסעיפים 15 או 16, לפי גילו, אם הוא רופא או רופא שיניים, בין שמלאו לו עשרים ושלוש שנים ובין שטרם מלאו לו עשרים ושלוש שנים;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;ומי שנקרא חייב להתייצב כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מי שנמצא בלתי כשר ארעית לשירות ובבדיקה חוזרת נמצא כשר לשירות, הרי אף אם נקרא לשירות סדיר, אך טרם השלים את תקופת השירות שהוא חייב בה לפי חוק זה, רשאי פוקד, בצו, לקרוא לו להתייצב להשלמת תקופת השירות שהוא חייב בה לפי חוק זה, ומי שנקרא חייב להתייצב כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;בכל בדיקה חוזרת תשתמש הועדה הרפואית במבחנים שהם בתוקף בשעת ביצוע הבדיקה החוזרת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif7" id="seif7">בדיקה נוספת [6]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">7. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">מצאה ועדה רפואית כי יוצא-צבא הוא כשר לשירות, או בלתי כשר לשירות או בלתי כשר ארעית לשירות, תמסור הועדה הודעה בכתב על החלטתה ליוצא-הצבא ולפוקד, והם רשאים, בתנאים שנקבעו בתקנות, לבקש כי הועדה הרפואית או הועדה הרפואית העליונה, לפי הענין, תבדוק את יוצא-הצבא בדיקה נוספת ותקבע את כשרו לשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">8. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif9" id="seif9">פעולות חיסון [7א]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">9. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">פוקד רשאי בצו, באישור שני רופאים מורשים, להורות כי מיועד לשירות בטחון יחוסן בהרכבות ובזריקות חיסון (להלן &ndash; פעולות חיסון) מפני מחלה פלונית בזמן ובמקום שינקוב בצו; מיועד לשירות בטחון שניתן עליו הצו חייב להתייצב ולאפשר את פעולות החיסון כמפורש בצו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif10' id='seif10'>תחולת חוקי שיקום על מי שנפגע מבדיקה או מחיסון [7ב ו-7ג]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">10. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מיועד לשירות בטחון או יוצא-צבא שאינו משרת שירות בטחון, שנפגע עקב בדיקה או פעולות חיסון לפי פרק זה, יחולו עליו ועל בני משפחתו חוקי השיקום, כאילו היה בשעת הפגיעה בשירות בטחון והפגיעה אירעה בתקופת שירותו ועקב שירותו, ואם לא שירת שירות בטחון אחרי הפגיעה, רואים אותו, לענין חוק הנכים (תגמולים ושיקום), תשי&quot;ט-1959 [נוסח משולב], כאילו שוחרר מהשירות במועד שבו נמצא, לפי חוק זה, בלתי כשר לשירות או הופטר ממנו; לענין סעיף זה, &quot;פגיעה&quot; &ndash; מחלה, החמרת מחלה או חבלה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;כל חיקוק המדבר בחוקי השיקום, ייקרא כאילו היה מדבר גם בסעיף קטן (א), והוא כשאין כוונה אחרת משתמעת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif11' id='seif11'>צו בדבר מתן אמצעי זיהוי [7ד] (תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">11. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">פוקד רשאי, בצו, לקרוא לכל מיועד לשירות בטחון או יוצא-צבא, להתייצב במקום ובזמן שקבע בצו הפוקד או מי שהוא הסמיך לכך, לשם מתן אמצעי זיהוי ששר הבטחון יקבע בתקנות; מי שהצו חל עליו חייב להתייצב במקום ובזמן שנקבעו כאמור לשם מתן אמצעי הזיהוי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name='seif12' id='seif12'>אי-התייצבות לבדיקה אינה פוטרת מהתייצבות לשירות בטחון [8] (תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">12. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מיועד לשירות בטחון שנקרא להתייצב לבדיקת כשרו לשירות בטחון ולא התייצב, או שהתייצב וסירב להיבדק או להשלים את הבדיקות, רשאי פוקד לקרוא לו להתייצב לשירות בטחון, לפי סעיפים 13 או 27 ולפי הענין, אם אותה שעה היה יוצא-צבא, אף על פי שכשרו לשירות בטחון עדיין לא נקבע; אך הוא לא יתחיל באימון צבאי כל עוד לא נבדק בדיקה רפואית לצורך קביעת כשרו הרפואי לשירות בטחון ונמצא כשר לשירות, והוראות סעיפים 5 עד 8 יחולו על הבדיקה, בשינויים המחוייבים לפי הענין; הבדיקה תיערך תוך חודש ימים לאחר התייצבותו, אלא אם כן אישר פוקד שהוא בדרגת אלוף משנה לפחות כי קיימים טעמים מיוחדים שיירשמו המצדיקים את הארכת התקופה; סירב יוצא הצבא להיבדק, יהא דינו כדין מי שהפר את סעיף 122 לחוק השיפוט הצבאי, תשט&quot;ו-1955.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 20) תשע&quot;ה-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(א1) &nbsp; &nbsp; &nbsp; &nbsp;במניין התקופה האמורה בסעיף קטן (א), לא יובאו בחשבון &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; תקופות שעל פי סעיף 18(א) ו-(ב) אין רואים אותן, לעניין חישוב זמן השירות, כתקופה שבה מילא אדם חובת שירות סדיר, ולעניין תקופות שבהן ריצה אותו אדם עונש מאסר או מחבוש בבית סוהר צבאי, מחנה מעצר צבאי או חדר משמר צבאי כמשמעותם בחוק השיפוט הצבאי, התשט&quot;ו-1955 &ndash; רק אם במהלך תקופות אלה סירב אותו אדם להיבדק או להשלים את הבדיקה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; תקופות שבהן אדם היה נתון במעצר על פי החלטה של בית דין צבאי או של בית משפט אחר, למעט מעצר פתוח על פי סעיף 244 לחוק השיפוט הצבאי, התשט&quot;ו-1955, ולעניין תקופות שבהן היה אותו אדם עצור בבית סוהר צבאי, מחנה מעצר צבאי או חדר משמר צבאי כמשמעותם בחוק השיפוט הצבאי, התשט&quot;ו-1955 &ndash; רק אם במהלך תקופות אלה סירב אותו אדם להיבדק או להשלים את הבדיקה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; תקופות שבהן אדם היה מנוע מלהמשיך בשירות תקין ביחידתו בשל פסק דין או החלטה של בית דין צבאי או של בית משפט אחר, ובכלל זה החלטה בדבר שחרור ממעצר בתנאים;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; תקופות שבהן נעדר אדם מן השירות ובשלהן הוכרז על פי פקודת הצבא כנפקד או כעריק.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 20) תשע&quot;ה-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;סעיף קטן (א) יחול, בשינויים המחוייבים, על יוצא צבא שנמצא בלתי כשר לשירות או בלתי כשר ארעית לשירות ונקרא להתייצב לבדיקה חוזרת לפי סעיף 6 ולא התייצב, או שהתייצב וסירב להיבדק או להשלים את הבדיקות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med3" id="med3">פרק ג&apos;: שירות סדיר</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif13" id="seif13">התייצבות לשירות סדיר [9]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">13. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">פוקד רשאי, בצו, לקרוא &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; ליוצא-צבא, גבר, שנמצא כשר לשירות והוא באחד הגילים שמשמונה עשרה עד עשרים ותשע, או שהוא רופא או רופא שיניים והוא באחד הגילים שמשלושים עד שלושים ושמונה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; ליוצא-צבא, אשה, שנמצאה כשרה לשירות והיא באחד הגילים משמונה עשרה עד עשרים ושש, או שהיא רופאה או רופאת שיניים והיא באחד הגילים שמעשרים ושבע עד שלושים ושמונה,</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;להתייצב, תוך התקופות הנזכרות בסעיף 20, לשירות סדיר במקום ובזמן שקבע בצו הפוקד, או מי שהוא הסמיך לכך, ואותו יוצא-צבא חייב להתייצב כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif14" id="seif14">התייצבות לשירות לפי בקשה [10] (תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">14. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;אזרח ישראלי או תושב קבוע, שעדיין לא הגיע לגיל שמונה עשרה והוא נמצא כשר לשירות, רשאי פוקד, בצו, לקרוא לו להתייצב לשירות סדיר, אם הוא ביקש זאת בכתב ואם הוריו או אפוטרופסו נתנו את הסכמתם לכך ומלאו לו שבע עשרה שנים; ואולם די בהסכמת הורה אחד אם קיים קושי של ממש לקיים קשר עם ההורה השני כדי לברר עמדתו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ניתן צו לפי סעיף זה, יחולו הוראות חוק זה על מי שהצו ניתן לגביו, כאילו היה יוצא-צבא שהגיע לגיל שמונה עשרה ונקרא להתייצב לשירות סדיר לפי סעיף 13.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif15" id="seif15">זמן השירות לגבר [11] (הוראת שעה) תשנ&quot;ה-1995 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">15. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">יוצא-צבא, גבר, שנקרא להתייצב לשירות סדיר לפי סעיפים 13 או 14, חייב בשירות סדיר &ndash;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;של שלושים חדשים &ndash; אם הוא נקרא להתייצב לשירות סדיר כשהוא באחד הגילים שמשמונה עשרה עד עשרים ושש;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">ת&quot;ט תשמ&quot;ו-1986</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;של עשרים ושישה חדשים &ndash; אם הוא נקרא להתייצב לשירות סדיר כשהוא באחד הגילים שמעשרים ושבע עד עשרים ותשע או כשהוא רופא או רופא שיניים והוא באחד הגילים שמשלושים עד שלושים וארבע; אלא שאם בא לארץ כעולה אחרי שהגיע לגיל עשרים ושבע יהיה חייב בשירות סדיר של עשרים חדשים בלבד;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp;של ארבעה עשר חדשים &ndash; אם הוא נקרא להתייצב לשירות סדיר כשהוא רופא או רופא שיניים והוא באחד הגילים משלושים וחמש עד שלושים ושמונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif16" id="seif16">זמן השירות לאשה [12]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">16. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">יוצא-צבא, אשה, שנקראה להתייצב לשירות סדיר לפי סעיפים 13 או 14, חייבת בשירות סדיר &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; של עשרים וארבעה חודשים &ndash; אם היא נקראה להתייצב לשירות סדיר כשהיא באחד הגילים שמשמונה עשרה עד עשרים ושש או כשהיא רופאה או רופאת שיניים והיא באחד הגילים שמעשרים ושבע עד שלושים וארבע; אלא שאם באה לארץ כעולה אחרי שהגיעה לגיל עשרים ושבע תהיה חייבת בשירות סדיר של שמונה עשר חדשים בלבד;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; של שנים עשר חדשים &ndash; אם היא נקראה להתייצב לשירות סדיר כשהיא רופאה או רופאת שיניים והיא באחד הגילים שמשלושים וחמש עד שלושים ושמונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif16a" id="seif16a">שוויון בשירות (תיקון מס&apos; 11) תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">16</span><span style="font-size:17px;font-family:Heebo-Regular;">א</span><span style="font-size:21px;font-family:Heebo-Regular;">.&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;לכל יוצא צבא אשה זכות שווה לזכותו של יוצא צבא גבר, למלא תפקיד כלשהו בשירות הצבאי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לא יראו פגיעה בזכותה של יוצא צבא אשה למלא תפקיד כלשהו, אם הדבר מתחייב ממהותו ומאופיו של התפקיד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; דין יוצא צבא אשה, המשרתת על פי התנדבותה, באחד התפקידים שקבע שר הביטחון באישור ועדת החוץ והביטחון של הכנסת, כדין יוצא צבא גבר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;(פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif17" id="seif17">התנדבות לשירות בטחון [13] (תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">17. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מי שאינו חייב בשירות סדיר רשאי להתנדב לשירות בכוחות הסדירים של צבא-הגנה לישראל, בתנאי שהגיע לפחות לגיל שבו היה מותר לקרוא אדם לשירות לפי סעיף 14.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;החייב בשירות סדיר לפי חוק זה רשאי להתנדב לתקופת שירות סדיר נוספת על התקופה שהוא חייב בה, בין אם טרם התחיל בשירות סדיר, בין אם הוא משרת אותה שעה בשירות כאמור ובין אם השתחרר משירותו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; התנדבות כאמור בסעיף זה תהא בהצהרה חתומה ביד המתנדב וטעונה אישור שר הבטחון; ההצהרה תפרט את תקופת ההתנדבות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;המשרת באישור שר הבטחון כמתנדב לפי סעיף זה, דינו לענין זכויותיו וחובותיו לפי כל חיקוק כדין מי שמשרת מכוח חוק זה שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;מי שהתנדב לשירות סדיר או לתקופת שירות סדיר נוספת, יהיה חייב בשירות עד תום התקופה הנקובה בהצהרת ההתנדבות, אלא אם כן הורה שר הבטחון על שחרורו במועד מוקדם יותר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; שר הבטחון רשאי לקבוע בתקנות תקופות התנדבות לשירות וכללים בדבר קבלת מתנדבים לשירות ובדבר שחרורם מהשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ז) &nbsp; תקופת שירות סדיר ששירת אדם כמתנדב תופחת מתקופת השירות הסדיר שיהיה חייב בה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ח) &nbsp;סעיף זה אינו חל על קבלה לשירות קבע ועל שחרור ממנו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif17a" id="seif17a">התנדבות לצורך מתן טיפול רפואי (תיקון מס&apos; 10) תשנ&quot;ט-1998 (תיקון מס&apos; 23) תשע&quot;ט-2019</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">17</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;בלתי כשר לשירות&quot; &ndash; (נמחקה);</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חייל&quot; &ndash; יוצא צבא, בשירות סדיר אשר במהלך טיפול רפואי בו נמצא בלתי כשר לשירות;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;יום האבחון&quot; &ndash; אחד מאלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; יום הפציעה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; היום שבו אובחן לראשונה המצב הבריאותי של יוצא הצבא בשירות סדיר, כפי שייקבע בתקנות לפי סעיף זה;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תקופת הטיפול&quot; &ndash; תקופה שבה זקוק יוצא צבא בשירות סדיר לטיפול רפואי, שאינו עולה על 180 ימים ושתחילתה ביום האבחון;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תקופת טיפול שניה&quot; &ndash; החלק מתקופת הטיפול שתחילתו 60 ימים מיום האבחון או ביום שבו נמצא יוצא הצבא בשירות סדיר בלתי כשר לשירות, לפי המאוחר.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;(1) &nbsp; חייל רשאי להתנדב לשירות סדיר לצורך קבלת טיפול רפואי, לתקופה שלא תעלה על תקופת הטיפול או עד שהוכר כמי שלקה בנכות לפי חוק הנכים (תגמולים ושיקום) [נוסח משולב], תשי&quot;ט-1959, לפי המוקדם;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; זכותו של חייל להתנדב כאמור בפסקה (1), תובא לידיעת החייל בדרך שתיקבע בפקודות הצבא;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; סעיף קטן זה לא יחול במקרים שבהם קבע שר הבטחון, באישור ועדת החוץ והבטחון של הכנסת, כי חייל אינו רשאי להתנדב.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בתקופת הטיפול השניה יהא החייל בשירות ללא תשלום ויחולו עליו הוראות חוק ביטוח בריאות ממלכתי, תשנ&quot;ד-1994; תקופת הטיפול השניה לא תיחשב כשירות סדיר לענין חוק קליטת חיילים משוחררים, תשנ&quot;ד-1994.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הבטחון, באישור ועדת החוץ והבטחון של הכנסת, רשאי לקבוע הוראות בכל ענין הנוגע לביצוע סעיף זה ובכלל זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; כללים, תנאים או מבחנים לקביעת יום האבחון, כאמור בפסקה (2) להגדרה &quot;יום האבחון&quot;;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הזכויות והחובות שיוטלו על חייל המשרת שירות סדיר לפי הוראות סעיף קטן (ב).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif17b" id="seif17b">דין חייל שנמצא בלתי כשר ארעית לשירות (תיקון מס&apos; 23) תשע&quot;ט-2019</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">17</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (א) &nbsp;בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חייל&quot; &ndash; יוצא צבא בשירות סדיר אשר במהלך טיפול רפואי בו נמצא בלתי כשר ארעית לשירות;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;יום האבחון&quot; &ndash; כהגדרתו בסעיף 17א;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תקופת אי-כשרות ארעית&quot; &ndash; תקופה שאינה עולה על 180 ימים שבמהלכה זקוק יוצא צבא בשירות סדיר לטיפול רפואי או אינו יכול לשרת בשידור סדיר מחמת חבלתו או מחלתו; תחילתה של תקופה זו ביום האבחון, וכל עוד הוא בלתי כשר ארעית לשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;חייל ימשיך בשירותו הסדיר עד תום תקופת אי-הכשרות הארעית, אלא אם כן הודיע לפוקד בכתב כי אינו מעוניין שיחולו עליו הוראות סעיף זה בתקופת אי-הכשרות הארעית; הודיע החייל לפוקד כאמור, יהיה רשאי להתנדב לצורך מתן טיפול רפואי לפי סעיף 17א.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; נמצא חייל כשר לשירות במהלך תקופת אי-הכשרות הארעית, לא יחולו עליו עוד הוראות סעיף זה וצבא הגנה לישראל יפעל לשלבו בתפקיד שבו שירת לפני שנמצא בלתי כשר ארעית לשירות, והכול בכפוף לצורכי כוח האדם ובהתאם למצבו הרפואי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;נמצא חייל בלתי כשר לשירות במהלך תקופת אי-הכשרות הארעית, יהיה החייל רשאי להתנדב לצורך מתן טיפול רפואי לפי סעיף 17א ותקופת הטיפול כהגדרתה בסעיף האמור תימנה מתחילת תקופת אי-הכשרות הארעית.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;הוראות סעיף זה יחולו כל עוד החייל משרת בשירות סדיר, או עד תום תקופת אי-הכשרות הארעית, לפי המוקדם.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; הוראות סעיף 17א(ב)(1) ו-(3) יחולו לעניין חייל לפי סעיף זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif18" id="seif18">זמן השירות לנעדר מן השירות [14] (תיקון מס&apos; 1) &nbsp;תשמ&quot;ד-1987</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">18. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;אדם שבתקופת שירותו הסדיר היה כלוא בשל עבירה על פי פסק דין של בית דין צבאי או של בית משפט אחר, או על פי פסק של קצין שיפוט בכיר, וכן אדם שנעדר מן השירות שלא כדין או שנעדר ברשות שהושגה בטענות כוזבות והורשע על כך כדין, ואם היתה לו זכות ערעור &ndash; לא ערער על ההרשעה או שערעורו על ההרשעה נדחה, אין רואים את תקופת כליאתו ואת תקופת העדרו לענין חישוב זמן השירות כתקופה שבה מילא חובת שירות סדיר, אלא אם כן הורה בית הדין הצבאי, בית המשפט או קצין השיפוט הבכיר הוראה אחרת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 3) תשמ&quot;ט-1989 (תיקון מס&apos; 15) תשס&quot;ח-2008 (תיקון מס&apos; 20) תשע&quot;ה-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לעניין סעיף קטן (א) יראו כתקופת כליאה תקופה שבה אדם &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; ריצה עונש מאסר או מחבוש בפועל;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; נשא עונש מאסר בעבודת שירות לפי חוק העונשין, תשל&quot;ז-1977, או בעבודה צבאית לפי חוק השיפוט הצבאי, תשט&quot;ו-1955;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוחזק במוסד סגור לפי סימן ז&apos; לפרק ו&apos; לחוק העונשין, תשל&quot;ז-1977.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 20) תשע&quot;ה-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; אדם שבתקופת שירותו הסדיר היה נתון במעצר למשך תקופה רצופה העולה על 14 ימים, על פי החלטה של בית דין צבאי או של בית משפט אחר, למעט מעצר פתוח על פי סעיף 244 לחוק השיפוט הצבאי, התשט&quot;ו-1955, וכן אדם שהיה מנוע מלשרת שירות תקין ביחידתו למשך תקופה רצופה העולה על 14 ימים, בשל פסק דין או החלטה של בית דין צבאי או של בית משפט אחר, ובכלל זה החלטה בדבר שחרור ממעצר בתנאים, אין רואים את התקופה שבה היה נתון במעצר או שבה היה מנוע מלשרת שירות תקין ביחידתו כאמור (בסעיף קטן זה &ndash; תקופת ההיעדרות), לעניין חישוב זמן השירות, כתקופה שבה מילא חובת שירות סדיר, אלא אם כן הורה בית הדין הצבאי או בית המשפט הוראה אחרת; ואולם אם זוכה בדין, יראו את תקופת ההיעדרות, לעניין חישוב זמן השירות, כתקופה שבה מילא חובת שירות סדיר, ובלבד שצירוף תקופת ההיעדרות ותקופת השירות ששירת בפועל לא יעלה על זמן השירות שהוא חייב בו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 20) תשע&quot;ה-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;נוסף על האמור בסעיף קטן (א), אדם שבתקופת שירותו הסדיר הוכרז לפי פקודת הצבא כעריק, אין רואים את התקופה שבה נעדר מן השירות אשר בשלה הוכרז כעריק (בסעיף קטן זה &ndash; תקופת ההיעדרות), לעניין חישוב זמן השירות, כתקופה שבה מילא חובת שירות סדיר; ואולם אם זוכה בדין לגבי ההיעדרות האמורה, יראו את תקופת ההיעדרות, לעניין חישוב זמן השירות, כתקופה שבה מילא חובת שירות סדיר, ובלבד שצירוף תקופת ההיעדרות ותקופת השירות ששירת בפועל לא יעלה על זמן השירות שהוא חייב בו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif19" id="seif19">דין עונש שהוטל לאחר שחרור משירות [14א]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">19. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">הורשע אדם כדין, ואם היתה לו זכות ערעור &ndash; לא ערער על ההרשעה או שערעורו על ההרשעה נדחה, לאחר שחרור משירות סדיר, בשל היעדרות מן השירות שלא כדין או בשל היעדרות ברשות שהושגה בטענות כוזבות, ובית הדין הצבאי או קצין השיפוט הבכיר לא הורה כי לענין חישוב זמן השירות יראו את תקופת ההיעדרות כתקופה שבה מילא אותו אדם חובת שירות סדיר, רשאי הפוקד לקראו להתייצב להשלמת תקופת השירות הסדיר שהיה חייב בו לפי חוק זה אילולא שוחרר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif20" id="seif20">תקופת הקריאה להתייצבות לשירות סדיר [15]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">20. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;יוצא-צבא לא ייקרא להתייצב לשירות סדיר אלא אם כן הזמן שנקבע להתייצבות הוא תוך תקופות אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לגבי מי שהיה אזרח ישראלי או תושב קבוע בהגיעו לגיל שמונה עשרה &ndash; תוך עשרים וארבעה חדשים מיום שהגיע לגיל שמונה עשרה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; לגבי מי שהיה לאזרח ישראלי או לתושב קבוע לאחר הגיעו לגיל שמונה עשרה &ndash; תוך עשרים וארבעה חדשים מיום שהיה לאזרח או לתושב כאמור, אולם לא תוך ששה חדשים מאותו יום אלא אם כן הסכים לכך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(א1) &nbsp; &nbsp; &nbsp; &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000 (תיקון מס&apos; 13) תשס&quot;ד-2004 (תיקון מס&apos; 17) תש&quot;ע-2010 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;יוצא-צבא שמועד התייצבותו לרישום, לבדיקה או לשירות ביטחון נדחה לפי בקשתו, בהתאם לסעיף 36 או בהתאם לסעיף 22ב או לפרק ג&apos;1, מותר לקרוא לו להתייצב לשירות סדיר, אם המועד שנקבע להתייצבותו הוא תוך שנים עשר חדשים מיום תום תקופת הדחיה או תוך התקופה שנקבעה בסעיף קטן (א), לפי המאוחר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000 (תיקון מס&apos; 13) תשס&quot;ד-2004 (תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; במנין התקופות להתייצבות לפי סעיפים קטנים (א) ו-(ב) לא יבואו התקופות האלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; תקופה שחלפה עקב אי קיום חובה על פי דין על ידי מיועד לשירות ביטחון, לרבות אי התייצבותו של המיועד לשירות ביטחון לרישום או לבדיקת כושרו לשירות ביטחון, או סירובו להיבדק או להשלים את הבדיקות;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; תקופה שאורכה נקבע בפקודות הצבא, אשר חלפה עקב טיפול בבקשתו של המיועד לשירות ביטחון, ושבשלה עוכבה קריאתו להתייצבות לשירות סדיר;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; תקופה העולה על חודשיים, שבה שהה מיועד לשירות ביטחון מחוץ לישראל, בין בהיתר לפי סעיף 43 ובין שלא בהיתר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000 (תיקון מס&apos; 13) תשס&quot;ד-2004 (תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג1) נמצא מיועד לשירות ביטחון בלתי כשר ארעית לשירות, לא תבוא במנין התקופות להתייצבות לפי סעיפים קטנים (א) ו-(ב) התקופה שנקבעה לענין זה בתקנות (להלן &ndash; תקופת אי כשירות); נמצא מיועד לשירות ביטחון בלתי כשר ארעית לשירות יותר מפעם אחת &ndash; לא יבואו במנין התקופות להתייצבות, כל תקופות אי הכשירות במצטבר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג2) חלפה התקופה לקריאה לשירות, רשאי פוקד לקרוא ליוצא צבא להתייצב לשירות סדיר, בתוך שנים עשר חודשים מתום התקופה לקריאה לשירות, ובלבד שממשך השירות שבו חייב יוצא הצבא, יופחת משך הזמן שחלף מתום התקופה לקריאה לשירות; לענין סעיף קטן זה, &quot;התקופה לקריאה לשירות&quot; &ndash; התקופה שבה ניתן היה לקרוא ליוצא הצבא להתייצב לשירות סדיר לפי הוראות סעיפים קטנים (א) עד (ג1).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 13) תשס&quot;ד-2004 (תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;סעיף קטן (א) לא יחול על מי שנקרא להתייצב לשירות סדיר לפי סעיף 6(ג) או (ד) או סעיף 19.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif21" id="seif21">חובת הכשרה חקלאית [16] (תיקון מס&apos; 2) תשמ&quot;ז-1987</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">21. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון רשאי, באישור ועדת החוץ והבטחון של הכנסת, לקבוע בתקנות כי תקופה שלא תעלה על שנים עשר החדשים הראשונים לשירותו הסדיר של יוצא צבא תוקדש, לאחר אימון צבאי ראשוני, להכשרה חקלאית או הכשרה חלוצית אחרת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;בתקנות כאמור רשאי שר הבטחון לקבוע גם את ענפי המשק שהכשרה בהם יראו כהכשרה חקלאית או הכשרה חלוצית אחרת, וכן את הסדרים והמשטר שינהגו בהכשרה כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif22" id="seif22">שמירה על גרעינים התיישבותיים [17] (תיקון מס&apos; 2) &nbsp;תשמ&quot;ז-1987</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון יתקין תקנות שתכליתן לשמור, תוך כדי ביצוע הוראות פרק זה, על שלמותם של גרעינים התיישבותיים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הוראות סעיף 21 אין כוחן יפה לגבי חברים לגרעין התיישבותי שחלות עליו תקנות שהותקנו על-פי סעיף קטן (א), פרט לחברי גרעין כזה הנשלחים להתאמן או לשרת בתפקידים פיקודיים של שירות בטחון ובתנאי שמספרם, ביחס ליתר חברי הגרעין, לא יעלה על האחוז שנקבע בתקנות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif22a" id="seif22a">ישיבות הסדר (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;ישיבת הסדר&quot; &ndash; ישיבה שמתקיימים בה לימודים תורניים, המיועדת למשרתים בשירות משולב והכלולה ברשימת ישיבות ההסדר שגיבש לעניין זה שר הביטחון לפי סעיף קטן (ד);</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שירות משולב&quot; &ndash; שירות סדיר המשלב פרק זמן או פרקי זמן של שירות צבאי פעיל ופרק זמן או פרקי זמן של לימוד בישיבת הסדר בתנאים של שירות בלא תשלום.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;יוצא צבא רשאי לשרת בשירות משולב, ובלבד שהוא עומד בתנאים שנקבעו בפקודות הצבא ובקשתו אושרה על פי הכללים שנקבעו בפקודות אלה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; תקופת השירות המשולב לא תפחת מתקופת השירות הסדיר שיוצא צבא חייב בה לפי חוק זה, בתוספת של 12 חודשים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הביטחון יגבש את רשימת ישיבות ההסדר בהתאם לאמות מידה שיקבע בתקנות; החלטת שר הביטחון בדבר הכללת ישיבת הסדר ברשימת הישיבות תיעשה בהתחשב בהמלצת ועד הישיבות בארץ ישראל או איגוד ישיבות ההסדר; אין בהוראות סעיף קטן זה כדי למנוע מישיבה לפנות לשר הביטחון בבקשה להיכלל ברשימת ישיבות ההסדר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;רשימת ישיבות ההסדר תפורסם באתר האינטרנט של אגף כוח אדם בצבא הגנה לישראל ותופקד לעיון הציבור הפוקד את לשכות הגיוס האזוריות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; שר הביטחון יקבע את הכללים להפעלת השירות המשולב ואת אמות המידה לאישור בקשות לפי סעיף זה, ובלבד שפרק הזמן הכולל של שירות צבאי פעיל במסגרת השירות המשולב, לא יפחת מ-17 חודשים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ז) &nbsp; תקנות לפי סעיף זה יותקנו באישור ועדת החוץ והביטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif22b" id="seif22b">דחיית שירות לתלמידי ישיבות גבוהות ציוניות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (א) &nbsp;בסעיף זה ובסעיפים 22ג עד 22ה, &quot;ישיבה גבוהה ציונית&quot; &ndash; ישיבה או כולל שמתקיימים בהם לימודים תורניים והכלולים ברשימת הישיבות הגבוהות הציוניות שגיבש לעניין זה שר הביטחון לפי סעיף 22ד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;נוכח ההכרה בחשיבות לימוד התורה, רשאי שר הביטחון, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה גבוהה ציונית, לדחות, בצו, את מועד התייצבותו לשירות סדיר, אם מצא כי מתקיימים לגביו התנאים לדחיית שירות כאמור בסעיף 22ג וראה לנכון לעשות כן בהתחשב בצורכי הביטחון ובהיקף הכוחות הסדירים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; צו דחיית שירות לפי סעיף זה יינתן לתקופה שלא תעלה על שנה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הביטחון רשאי לחזור ולתת למיועד לשירות ביטחון כאמור בסעיף קטן (ב) צו דחיית שירות לפי סעיף זה, עד הגיעו לגיל 23.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;(1) &nbsp; על אף הוראות סעיף קטן (ד), שר הביטחון רשאי לחזור ולתת, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה גבוהה ציונית, צו דחיית שירות לפי סעיף זה לתקופות נוספות שלא יעלו על שנה כל אחת, עד הגיעו לגיל 26;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מספר המבקשים שיינתן להם צו דחיית שירות לראשונה לפי סעיף קטן זה, בשנת גיוס מסוימת, לא יעלה על 300; בסעיף זה, &quot;שנת גיוס&quot; &ndash; שנה המתחילה ב-1 ביולי והמסתיימת ב-30 ביוני;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; עלה, בשנת גיוס מסוימת, מספר המבקשים לראשונה דחיית שירות לפי סעיף קטן זה על 300, ידחה שר הביטחון את מועד התייצבותם של 300 מבקשי דחיית שירות בלבד, בהתאם לאמות מידה שקבע.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; בוטל או פקע תוקפו של צו דחיית שירות שניתן לפי סעיף זה יחולו על המיועד לשירות ביטחון הוראות סעיף 13.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif22c" id="seif22c">תנאים לדחיית שירות לתלמידי ישיבות גבוהות ציוניות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. &nbsp; (א) &nbsp;לא יינתן צו דחיית שירות לפי סעיף 22ב אלא למיועד לשירות ביטחון שהוא תלמיד ישיבה גבוהה ציונית שמתקיימים בו כל אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הוא לומד בישיבה גבוהה ציונית לימודים תורניים, באופן סדיר, בהיקף שלא יפחת מ-45 שעות בשבוע, ובכולל &ndash; 40 שעות בשבוע, למעט בתקופות חופשה שקבע שר הביטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הוא לא עוסק בכל עיסוק נוסף על לימודיו בישיבה; בפסקה זו, &quot;עיסוק&quot; &ndash; עבודה שמקובל לקבל בעבורה שכר, בין שהיא נעשית בשכר ובין שנעשית שלא בשכר, ולרבות כל עיסוק אחר שקבע שר הביטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוא קיים את חובותיו לפי חוק זה, ובכלל זה התייצב לרישום ולבדיקה רפואית לפי סעיפים 3 ו-5;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; הוא הצהיר, בתצהיר בכתב שניתן לפי סעיף 15 לפקודת הראיות [נוסח חדש], התשל&quot;א-1971 (בחוק זה &ndash; פקודת הראיות), כי מתקיימים בו התנאים שבפסקאות (1) עד (3);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(5) &nbsp; ראש הישיבה שבה הוא לומד אישר, בתצהיר בכתב שניתן לפי סעיף 15 לפקודת הראיות, כי מתקיים במיועד לשירות ביטחון התנאי שבפסקה (1), והתחייב כי אם יחדל להתקיים במיועד לשירות ביטחון התנאי האמור, יודיע על כך לפוקד בתוך 21 ימים;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(6) &nbsp; תנאים מוקדמים נוספים שקבע שר הביטחון, באישור ועדת החוץ והביטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ביקש מיועד לשירות ביטחון לחזור ולקבל צו דחיית שירות לפי סעיף 22ב, יצרף מחדש לכל בקשה כאמור תצהירים והתחייבות כאמור בסעיף קטן (א)(4) ו-(5).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">רשימת הישיבות הגבוהות הציוניות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22</span><span style="font-size:17px;font-family:Heebo-Regular;">ד. &nbsp; (א) &nbsp;שר הביטחון יגבש את רשימת הישיבות הגבוהות הציוניות בהתאם לאמות מידה שיקבע בתקנות, ובלבד שמתקיימים בישיבה כל אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; מרבית תלמידיה הם בוגרי מוסד חינוך על-יסודי הנתון לפיקוחו של מינהל החינוך הדתי במשרד החינוך;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מתקיימים בה לימודים בהיקף שלא יפחת מ-45 שעות בשבוע, ובכולל &ndash; 40 שעות בשבוע;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; תנאים וכללים נוספים שקבע שר הביטחון באישור ועדת החוץ והביטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;החלטת שר הביטחון בדבר הכללת ישיבה גבוהה ציונית ברשימת הישיבות תיעשה בהתחשב בהמלצת איגוד הישיבות הגבוהות הציוניות; אין בהוראות סעיף קטן זה כדי למנוע מישיבה לפנות לשר הביטחון בבקשה להיכלל ברשימת הישיבות הגבוהות הציוניות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; רשימת הישיבות הגבוהות הציוניות תפורסם באתר האינטרנט של אגף כוח אדם בצבא הגנה לישראל ותופקד לעיון הציבור הפוקד את לשכות הגיוס האזוריות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הביטחון רשאי להסיר ישיבה מרשימת הישיבות הגבוהות הציוניות, לאחר שנתן לראש הישיבה הזדמנות להשמיע את טענותיו לפניו, אם מצא כי התקיים אחד מאלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; ראש הישיבה אינו ממלא את חובותיו בהתאם להוראות סעיף 22ג(א)(5);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; חדל להתקיים בישיבה אחד מהתנאים בהתאם לאמות המידה שקבע שר הביטחון כאמור בסעיף קטן (א);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; התקיימו נסיבות אחרות המצדיקות, לדעת שר הביטחון, את הסרתה מהרשימה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif22e" id="seif22e">דיווח לכנסת &ndash; ישיבות גבוהות ציוניות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">22</span><span style="font-size:17px;font-family:Heebo-Regular;">ה. &nbsp; שר הביטחון ידווח לוועדת החוץ והביטחון של הכנסת, אחת לשנה, לגבי השנה שחלפה, על עניינים אלה:</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;מספר צווי דחיית השירות משירות סדיר שניתנו לפי סעיף 22ב;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;מספר המתייצבים לשירות סדיר בכלל, ומספר המתייצבים כאמור מקרב תלמידי הישיבות הגבוהות הציוניות בפרט;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp;נתונים בעניינים המפורטים בפסקאות (1) ו-(2) בהשוואה לשנים קודמות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif23" id="seif23">הבטחת השכלה יסודית [18]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">23. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">חייל המשרת בשירות סדיר ורמת השכלתו נמוכה מרמה של השכלה יסודית שנקבעה בתקנות, שלושה חדשים משירותו הסדיר יוקצו להקניית השכלה, באופן ובתנאים שנקבעו בתקנות; התקנות יותקנו בהסכמת שר החינוך והתרבות, לאחר התייעצות עם ועדת החוץ והבטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif24" id="seif24">שירות במשמר הגבול [18א ו-18ב] (תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">24. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון, בהתייעצות עם שר המשטרה או עם מי שהוא הסמיך לכך</span><u><sup><span dir="LTR" style="font-size:17px;font-family:Heebo-Regular;color:blue;">[1]</span></sup></u><span style="font-size:17px;font-family:Heebo-Regular;">, רשאי להורות בצו כי יוצא-צבא, שנמצא כשר לשירות והוא באחד הגילים שמשמונה עשרה עד עשרים ותשע, ישרת במשמר הגבול שבמשטרת ישראל (להלן &ndash; משמר הגבול) כל תקופת השירות הסדיר שהוא חייב בה או חלק ממנה, הכל כאמור בצו &ndash; בין שהתחיל בשירותו זה ובין אם לאו &ndash; ועליו להתייצב לשירות במשמר הגבול בהתאם לאמור בצו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 7) &nbsp;תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;שירות במשמר הגבול לפי חוק זה, דינו לענין חוק זה, חוקים הנוגעים לחיילים משוחררים וחוקי השיקום כדין שירות סדיר, והוא כשאין הוראה מפורשת אחרת בחוק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif24a" id="seif24a">שירות ביחידות אחרות של משטרת ישראל (הוראת שעה) &nbsp;תשנ&quot;ה-1995 (תיקון מס&apos; 2) &nbsp;תשנ&quot;ח-1998 (תיקון מס&apos; 3) תש&quot;ס-2000 (תיקון מס&apos; 15) תשע&quot;ו-2016</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">24</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;שר הבטחון, בהסכמת השר לביטחון הפנים, רשאי להורות בצו כי יוצא-צבא שנמצא כשר לשירות והוא באחד הגילים משמונה עשרה עד עשרים ותשע, ישרת ביחידות של משטרת ישראל שאינן משמר הגבול, ושעיקר פעילותן הוא בטחון המדינה ותושביה (להלן &ndash; יחידות אחרות במשטרת ישראל).</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;(1) &nbsp; יוצא-צבא, שהוצב לשרת ביחידות אחרות במשטרת&emsp;ישראל, רשאי, משהודע לו על הצבתו, להביע את התנגדותו בפני הקצין שהודיע לו על כך, או בפני קצין מוסמך; הודעה כאמור תימסר לא יאוחר מחודשיים מיום הצבתו כאמור, ומשעשה כן &ndash; יוצב לשירות בצבא הגנה לישראל;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; אין בהוראות פסקה (1) כדי לגרוע מזכותו של יוצא צבא שהוצב ביחידה אחרת במשטרת ישראל, להגיש בקשה בכל עת, בהתאם לפקודות הצבא, לעבור לשרת ביחידות צבא הגנה לישראל; עשה כן, תידון בקשתו ותישקל בדרך המקובלת בצבא.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; בסעיף קטן זה, &quot;קצין מוסמך&quot; &ndash; קצין צבא שהוסמך לענין זה על ידי ראש אגף כוח אדם בצבא הגנה לישראל.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מספר יוצאי הצבא שישרתו ביחידות אחרות במשטרת ישראל ייקבע בידי שר הבטחון, בהסכמת השר לביטחון הפנים, ובאישור הממשלה וועדת החוץ והבטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;דינו של יוצא-צבא המשרת ביחידות אחרות במשטרת ישראל, כדין יוצא-צבא המשרת במשמר הגבול והוראות סעיפים 24, 25 ו-26, יחולו עליו בשינויים המחוייבים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif24b" id="seif24b">שירות ביחידות ביטחוניות בשירות בתי הסוהר (הוראת שעה) תשס&quot;ה-2005 (הוראת שעה) (תיקון מס&apos; 3) תשס&quot;ט-2009</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">24</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (א) &nbsp;שר הביטחון, בהסכמת השר לביטחון הפנים, רשאי להורות בצו כי יוצא צבא שנמצא כשר לשירות והוא באחד הגילים משמונה עשרה עד עשרים ותשע, ישרת ביחידות של שירות בתי הסוהר שתפקידן הוא אבטחה של בתי סוהר המשמשים להחזקת אסירים ביטחוניים בלבד או של אגפים בבתי הסוהר, המשמשים להחזקת אסירים ביטחוניים בלבד, ולצורך אבטחתם של אסירים כאמור וליווים וכן ביחידות אחרות של שירות בתי הסוהר, בתפקידים של שירותי מינהלה הנוגעים לארגון כוח אדם וטיפול בפרט שיינתנו רק ליוצא צבא שהוצב לשרת בשירות בתי הסוהר, ששר הביטחון קבע בצו (בסעיף זה &ndash; יחידות ביטחוניות בשירות בתי הסוהר).</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;(1) &nbsp; יוצא צבא שהוצב לשרת בשירות בתי הסוהר רשאי, משנודע לו על הצבתו, להביע את התנגדותו לפני הקצין שהודיע לו על כך, או לפני קצין מוסמך; הודעה כאמור תימסר לא יאוחר מארבעה חודשים מיום שהודע לו על הצבתו או שלושה חודשים מיום הצבתו בפועל, לפי המאוחר מביניהם, ומשעשה כן &ndash; יוצב לשירות בצבא הגנה לישראל.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; אין בהוראות פסקה (1) כדי לגרוע מזכותו של יוצא צבא שהוצב ביחידות ביטחוניות בשירות בתי הסוהר להגיש בקשה, בכל עת, בהתאם לפקודות הצבא, לעבור לשרת ביחידות צבא הגנה לישראל; עשה כן, תידון בקשתו ותישקל בדרך המקובלת בצבא.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; בסעיף קטן זה, &quot;קצין מוסמך&quot; &ndash; קצין צבא שהוסמך לענין זה על ידי ראש אגף כוח אדם בצבא הגנה לישראל.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מספר יוצאי הצבא שישרתו ביחידות ביטחוניות בשירות בתי הסוהר ייקבע בידי שר הביטחון, בהסכמת השר לביטחון הפנים, ובאישור הממשלה וועדת החוץ והביטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;הוראות סעיפים 24 ו-26 יחולו לגבי יוצא צבא המשרת ביחידות ביטחוניות בשירות בתי הסוהר, בשינויים המחויבים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) (תיקון מס&apos; 3) תשס&quot;ט-2009</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;השר לביטחון הפנים ימסור לוועדת החוץ והביטחון של הכנסת דיווח שנתי על מספר יוצאי הצבא המוצבים בכל יחידה של שירות בתי הסוהר לפי הוראות סעיף זה ועל התפקידים שבהם הם משרתים והמשימות שהם מבצעים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif24c" id="seif24c">סמכויות וחובות של המשרת בשירות בתי הסוהר (הוראת שעה) תשס&quot;ה-2005</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">24</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. &nbsp; (א) &nbsp;כל עוד יוצא צבא משרת בהתאם לצו לפי סעיף 24ב, לא יראונו לענין חוק השיפוט הצבאי, התשט&quot;ו-1955, והתקנות לפיו, כחייל כהגדרתו בסעיף 1 לאותו חוק, ואולם סעיפים 250ב ו-250ג לאותו חוק יחולו באופן שהסמכות הנתונה בהם לקצין משטרה צבאית שהוא קצין שיפוט בכיר תהא מסורה לסוהר בכיר שמונה לענין זה על ידי נציב בתי הסוהר, וביצוע הבדיקות ייוחד לגילוי שימוש בסם מסוכן או החזקת סם מסוכן בשירות בתי הסוהר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;דינו של יוצא צבא המשרת כאמור הוא כדין סוהר בכל הנוגע לסמכויותיו וחובותיו, לרבות כללי המשמעת, ואולם &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לא יחולו עליו הוראות סעיף 80(ג)(2) בפקודת בתי הסוהר [נוסח חדש], התשל&quot;ב-1971, בדבר הסמכות לשחררו או לפטרו, והוראות סעיפים 82, 83, 86 ו-87 בפקודה האמורה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; שכרו יהיה כשכרו של יוצא צבא המשרת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif25" id="seif25">סמכויות וחובות של המשרת במשמר הגבול [18ג] (תיקון מס&apos; 8) &nbsp;תשנ&quot;ו-1996</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">25. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;כל עוד יוצא-צבא משרת בהתאם לצו לפי סעיף 24(א), לא יראוהו לענין חוק השיפוט הצבאי, תשט&quot;ו-1955, והתקנות לפיו כחייל כמשמעותו בסעיף 1 לאותו חוק, ואולם סעיפים 250ב ו-250ג לאותו חוק יחולו באופן שהסמכות הנתונה בהם לקצין משטרה צבאית שהינו קצין שיפוט בכיר תהא מסורה לקצין משטרה בכיר שמונה לענין זה על ידי המפקח הכללי של משטרת ישראל, וביצוע הבדיקות ייוחד לגילוי שימוש בסם מסוכן או החזקת סם מסוכן במשטרת ישראל.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;דינו של יוצא-צבא המשרת כאמור הוא כדין שוטר בכל הנוגע לסמכויותיו וחובותיו, לרבות כללי המשמעת, ואולם &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לא יחולו עליו הוראות סעיף 10(2) לפקודת המשטרה [נוסח חדש], תשל&quot;א-1971, בדבר הסמכות לשחררו או לפטרו, והוראות סעיפים 11, 12, 13 ו-18 עד 20 וכן הפרק הרביעי לפקודה האמורה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; שכרו יהיה כשכרו של יוצא-צבא המשרת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26" id="seif26">המשך השירות הסדיר אחרי גמר השירות במשמר הגבול [18ד]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">יוצא-צבא ששירותו במשמר הגבול בא לידי גמר בהתאם לאמור בצו לפי סעיף 24(א) או עקב ביטולו של הצו, ותקופת השירות הסדיר שהיה חייב בה אלמלא הצו עדיין לא עברה, יהיה חייב בשירות סדיר עד תום התקופה האמורה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-1" id="seif26-1">שירות מוכר (תיקון מס&apos; 7) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;בסעיף זה, &quot;שירות מוכר&quot; &ndash; שירות לפי פסקאות (1) או (2), ששר הבטחון קבע בצו באישור הממשלה וועדת החוץ והבטחון של הכנסת &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; שירות ביחידות צבאיות במסגרת משרד הבטחון ויחידות סמך של משרד הבטחון ומשרד ראש הממשלה שתכלית פעילותן היא בטחון המדינה ותושביה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; שירות ביחידות צבאיות במסגרת משרד ממשלתי או במסגרת הארגונית של גוף ציבורי ובפיקוח משרד ממשלתי, שתכליתו להשיג יעד בטחוני-לאומי באחד התחומים הבאים: עליה וקליטה, חינוך, בריאות, הגנת העורף או פעולות התנדבות למען חיילי צה&quot;ל, והכל אם שוכנע שר הבטחון, בשים לב לנסיבות הענין באותה העת, ובהתייעצות, לפי הענין, עם השר לקליטת העליה, שר החינוך, התרבות והספורט או שר הבריאות וכן עם שר המשפטים, כי אם לא תיעשה הפעילות בידי יוצא-צבא בשירות סדיר לא יושג היעד כנדרש.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו לפי סעיף קטן (א) יפרט את המשרדים הממשלתיים, את הגופים הציבוריים ואת השירות המוכר שייעשה בהם בהתאם לסעיף קטן (א); צו כאמור יינתן לתקופה שלא תעלה על שלוש שנים, וניתן להאריכו, באותה דרך, לתקופות נוספות שלא יעלו על שלוש שנים כל אחת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הבטחון רשאי להורות בצו כי יוצא-צבא, שנמצא כשר לשירות ועבר אימון צבאי ראשוני, ישרת משך כל תקופת השירות הסדיר או חלק ממנה, במסגרת של שירות מוכר; לא יורה השר על שירותו של יוצא-צבא במסגרת שירות מוכר לפי סעיף קטן (א)(2) אלא אם כן נתן יוצא-הצבא את הסכמתו לכך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שירות מוכר דינו כדין שירות סדיר לכל דבר וענין.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;שר הבטחון יקבע את ההסדרים, לענין השירות המוכר, שיחולו על המשרד הממשלתי ועל הגוף הציבורי שבמסגרת הארגונית שלו משרת יוצא-צבא.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; שר הבטחון ימציא לועדת החוץ והבטחון של הכנסת, אחת לשנה, דין וחשבון על שירותם של יוצאי צבא ביחידות אחרות של משטרת ישראל ובשירות מוכר, וכן ימציא העתקו של דין וחשבון זה לוועדת העבודה והרווחה של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med3a" id="med3a">פרק ג&apos;1: שילוב בוגרי ישיבות ומוסדות חינוך חרדיים</span></strong><strong><u><sup><span dir="LTR" style="font-size:16px;font-family:Heebo-Regular;color:blue;">[2]</span></sup></u></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign1" id="sign1">סימן א&apos;: פרשנות ותחולה</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-2" id="seif26-2">הגדרות &ndash; פרק ג&apos;1 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; בפרק זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;חוק שירות לאומי-אזרחי&quot; &ndash; חוק שירות לאומי-אזרחי, התשע&quot;ד-2014;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;יום התחילה&quot; &ndash; י&quot;ח באדר ב&apos; התשע&quot;ד (20 במרס 2014);</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;ישיבה&quot; &ndash; ישיבה או כולל שמתקיימים בהם לימודים תורניים והכלולים ברשימה שגיבש לעניין זה שר הביטחון לפי סעיף 26כד;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שירות לאומי-אזרחי&quot;, &quot;שירות אזרחי-ביטחוני&quot; ו&quot;שירות אזרחי-חברתי&quot; &ndash; כהגדרתם בחוק שירות לאומי-אזרחי;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;שנת גיוס&quot; &ndash; שנה המתחילה ב-1 ביולי והמסתיימת ב-30 ביוני;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תנאים לדחיית שירות&quot; &ndash; התנאים המנויים בסעיף 26כג;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תקופת ההסתגלות הראשונה&quot; &ndash; התקופה שמיום התחילה עד יום ח&apos; בתמוז התש&quot;ף (30 ביוני 2020);</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תקופת ההסתגלות השנייה&quot; &ndash; התקופה שמיום ט&apos; בתמוז התש&quot;ף (1 ביולי 2020) עד יום י&quot;א בתמוז התשפ&quot;ג (30 ביוני 2023).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-3" id="seif26-3">תחולה &ndash; פרק ג&apos;1 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. &nbsp; (א) &nbsp;הוראות פרק זה יחולו נוכח ההכרה בחשיבות לימוד התורה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הוראות פרק זה יחולו כמפורט להלן:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; מי שביום התחילה מלאו לו 18 שנים וטרם מלאו לו 22 שנים, יחולו עליו הוראות סעיפים 26ה עד 26ח;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מי שביום התחילה מלאו לו 22 שנים, יחולו עליו הוראות סעיפים 26ט ו-26י;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; מי שמלאו לו 18 שנים במהלך תקופת ההסתגלות הראשונה, יחולו עליו הוראות סעיף 26יא;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; מי שמלאו לו 18 שנים במהלך תקופת ההסתגלות השנייה, יחולו עליו הוראות סעיף 26יח.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign2" id="sign2">סימן ב&apos;: תקופת ההסתגלות הראשונה</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-4" id="seif26-4">תחולה &ndash; תקופת ההסתגלות הראשונה (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ד. &nbsp; הוראות סימן זה יחולו בתקופת ההסתגלות הראשונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-5" id="seif26-5">דחיית שירות לתלמיד ישיבה שמלאו לו 18 שנים וטרם מלאו לו 22 שנים (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ה. &nbsp; (א) &nbsp;שר הביטחון, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה וביום התחילה מלאו לו 18 שנים וטרם מלאו לו 22 שנים, רשאי לדחות, בצו, את מועד התייצבותו של המבקש לשירות סדיר, אם מצא כי מתקיימים לגביו התנאים לדחיית שירות, וראה לנכון לעשות כן בהתחשב בצורכי הביטחון ובהיקף הכוחות הסדירים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו דחיית שירות לפי סעיף זה יינתן לתקופה שלא תעלה על שנה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הביטחון רשאי לחזור ולתת למיועד לשירות ביטחון כאמור בסעיף קטן (א) צו דחיית שירות לפי סעיף זה, עד הגיעו לגיל 24.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;נדחה שירותו של מיועד לשירות ביטחון לפי הוראות סעיף זה, מזמן לזמן, עד הגיעו לגיל 24, רשאי שר הביטחון לפטור אותו, בצו, מחובת שירות סדיר, ויחולו לגביו הוראות סעיף 26יב לעניין שילוב בתעסוקה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-6" id="seif26-6">ביטול או פקיעת תוקף של צו דחיית שירות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ו. &nbsp; (א) &nbsp;בוטל צו דחיית שירות שניתן לפי סעיף 26ה בשל אי-עמידה בתנאים לדחיית שירות, יחולו על המיועד לשירות ביטחון הוראות סעיף 13.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;פקע תוקפו של צו דחיית שירות שניתן לפי סעיף 26ה, ולא ביקש המיועד לשירות ביטחון להאריכו, יחולו עליו הוראות סעיף 13 אם טרם מלאו לו 21 שנים, והוראות סעיף 26ז &ndash; אם מלאו לו 21 שנים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-7" id="seif26-7">קריאה לשירות סדיר או הפניה לשירות לאומי-אזרחי (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ז. &nbsp; (א) &nbsp;מיועד לשירות ביטחון שניתן לו צו דחיית שירות לפי פרק זה, ופקע תוקפו של הצו לאחר שמלאו לו 21 שנים, רשאי פוקד, בצו, לקרוא לו להתייצב לשם בדיקת התאמתו לשרת בשירות ביטחון במקום ובזמן שהפוקד או מי שהוא הסמיך לכך קבע בצו; מי שנקרא להתייצב בצו לפי סעיף קטן זה חייב להתייצב במקום ובזמן שנקבעו בצו כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מצא הפוקד כי המיועד לשירות ביטחון מתאים לשרת בשירות ביטחון, רשאי הוא, בצו, לקרוא לו להתייצב, בתוך התקופות הנזכרות בסעיף 20, לשירות סדיר, במקום ובזמן שקבע, בצו, הפוקד או מי שהוא הסמיך לכך, והמיועד לשירות ביטחון חייב להתייצב כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; על אף הוראות סעיף קטן (ב), הפוקד רשאי, לאחר שבחן את כל אפשרויות הצבתו בשירות סדיר של המיועד לשירות ביטחון, להפנותו לשירות לאומי-אזרחי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;הסכים המיועד לשירות ביטחון להפנייתו לשירות לאומי-אזרחי, ידחה שר הביטחון, בצו, את מועד התייצבותו לשירות סדיר לצורך ביצוע השירות הלאומי-אזרחי; לא הסכים המיועד לשירות ביטחון להפנייתו כאמור, רשאי הפוקד, בצו, לקרוא לו להתייצב לשירות סדיר ויחולו הוראות סעיף קטן (ב).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-8" id="seif26-8">פטור משירות סדיר למי ששירת בשירות לאומי-אזרחי כאמור בסעיף 26ז (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ח. &nbsp;(א) &nbsp;מי ששירת בשירות לאומי-אזרחי כאמור בסעיף 26ז וניתן לו אישור על השלמת השירות לפי סעיף 12 לחוק שירות לאומי-אזרחי, יפטור אותו שר הביטחון, בצו, מחובת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הופסק השירות הלאומי-אזרחי של מיועד לשירות ביטחון לפי הוראות סעיף 23 לחוק שירות לאומי-אזרחי, רשאי הפוקד, בצו, לקרוא לו להתייצב לשירות סדיר, ויחולו הוראות סעיף 26ז(ב).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-9" id="seif26-9">הוראות לעניין מי שמלאו לו 22 שנים (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">ט. &nbsp;(א) &nbsp;מיועד לשירות ביטחון שניתן לו צו דחיית שירות לפי חוק דחיית שירות לתלמידי ישיבות שתורתם אומנותם, התשס&quot;ב-2002, והצו עמד בתוקפו ביום הפקיעה של החוק האמור בי&quot;ג באב התשע&quot;ב (1 באוגוסט 2012), וביום התחילה מלאו לו 22 שנים, רשאי הפוקד, בצו, לקרוא לו להתייצב לשם בחינת שילובו בשירות סדיר או בשירות לאומי-אזרחי, במקום ובזמן שהפוקד או מי שהוא הסמיך לכך קבע בצו; מי שנקרא להתייצב בצו לפי סעיף קטן זה, חייב להתייצב במקום ובזמן שנקבעו בצו כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;בחר המיעוד לשירות ביטחון להשתלב בשירות סדיר, רשאי הפוקד או מי שהוא המסיך לכך, בצו, לקרוא לו להתייצב במקום ובזמן שקבע בצו, לשם בדיקת התאמתו לשרת בשירות ביטחון, והוא חייב להתייצב במקום ובזמן שנקבעו בצו כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מצא הפוקד כי המיועד לשירות ביטחון מתאים לשרת בשירות ביטחון, רשאי הוא, בצו, לקרוא לו להתייצב, בתוך תקופות הקריאה להתייצבות הנזכרות בסעיף 20, לשירות סדיר, במקום ובזמן שקבע, בצו, הפוקד או מי שהוא הסמיך לכך, והוא חייב להתייצב במקום ובזמן שנקבעו בצו כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מצא הפוקד כי מיועד לשירות ביטחון שבחר לשרת בשירות סדיר אינו מתאים לשרת בשירות ביטחון, לאחר שבחן את כל אפשרויות הצבתו של המיועד לשירות ביטחון בשירות סדיר, רשאי הוא, בצו, לקרוא לו להתייצב לשם בחינת שילובו בשירות לאומי-אזרחי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;בחר המיועד לשירות ביטחון להשתלב בשירות לאומי-אזרחי, יפנה אותו הפוקד לשירות לאומי-אזרחי, ושר הביטחון ידחה, בצו, את מועד התייצבותו לשירות סדיר לשם ביצוע השירות הלאומי-אזרחי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; בחר המיועד לשירות ביטחון שלא להשתלב בשירות סדיר או בשירות לאומי-אזרחי, רשאי שר הביטחון לפטור אותו, בצו, מחובת שירות סדיר, ויחולו לגביו הוראות סעיף 26יב לעניין שילוב בתעסוקה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-10" id="seif26-10">פטור משירות סדיר למי ששירת בשירות לאומי-אזרחי כאמור בסעיף 26ט (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">י. &nbsp; &nbsp;(א) &nbsp;מי ששירת בשירות לאומי-אזרחי כאמור בסעיף 26ט וניתן לו אישור על השלמת השירות לפי סעיף 12 לחוק שירות לאומי-אזרחי, יפטור אותו שר הביטחון, בצו, מחובת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הופסק השירות הלאומי-אזרחי של מיועד לשירות ביטחון לפי הוראות סעיף 23 לחוק שירות לאומי-אזרחי, רשאי הפוקד, בצו, לקרוא לו להתייצב לשירות סדיר, ויחולו הוראות סעיף 26ז(ב).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-11" id="seif26-11">הוראות לעניין מי שמלאו לו 18 שנים במהלך תקופת ההסתגלות הראשונה (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יא. &nbsp;(א) &nbsp;שר הביטחון, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה שימלאו לו 18 שנים במהלך תקופת ההסתגלות הראשונה, רשאי לדחות, בצו, את מועד התייצבותו של המבקש לשירות סדיר, אם מצא כי מתקיימים לגביו התנאים לדחיית שירות, וראה לנכון לעשות כן בהתחשב בצורכי הביטחון ובהיקף הכוחות הסדירים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו דחיית שירות לפי סעיף זה יינתן לתקופה שלא תעלה על שנה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הביטחון רשאי במהלך תקופת ההסתגלות הראשונה לחזור ולתת למיועד לשירות ביטחון כאמור בסעיף קטן (א) צו דחיית שירות לפי סעיף זה, עד הגיעו לגיל 24.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;על מיועד לשירות ביטחון שמועד התייצבותו לשירות סדיר נדחה לפי סעיף זה, יחולו הוראות אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; מלאו לו 24 שנים במהלך תקופת ההסתגלות הראשונה &ndash; רשאי שר הביטחון לפטור אותו בהגיעו לגיל האמור, בצו, מחובת שירות סדיר, ויחולו לגביו הוראות סעיף 26יב לעניין שילוב בתעסוקה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; לא מלאו לו 24 שנים עד תום תקופת ההסתגלות הראשונה &ndash; יחולו עליו, בתום תקופת הצו האחרון לדחיית שירות שניתן לפי סעיף זה, הוראות סימן ה&apos;.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;בוטל צו דחיית שירות שניתן לפי סעיף זה או פקע תוקפו, יחולו הוראות סעיף 26ו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign3" id="sign3">סימן ג&apos;: שילוב בתעסוקה</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-12" id="seif26-12">שילוב בתעסוקה (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יב. &nbsp;(א) &nbsp;לשם קידום שילובם של בוגרי הישיבות בתעסוקה, רשאי עובד משרד הכלכלה ששר הכלכלה</span><u><sup><span dir="LTR" style="font-size:17px;font-family:Heebo-Regular;color:blue;">[3]</span></sup></u><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;הסמיכו לכך לקבל מפוקד או מאדם מטעמו מידע שיש בידיו לגבי מי ששר הביטחון פטר אותו מחובת שירות סדיר לפי סעיף 26ה(ד) או 26ט(ו) (בסעיף זה &ndash; מקבל פטור); מידע כאמור יימסר למטרת פנייה של משרד הכלכלה או של מפעיל למקבל הפטור; עובד משרד הכלכלה יבקש מפוקד מידע לפי פסקה זו רק לתכלית האמורה ובמידה שנדרש.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מידע יועבר לפי הוראות סעיף קטן (א) באופן מאובטח בהתאם לנהלים שייקבעו במשרד הכלכלה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מקבל פטור שמידע לגביו הועבר לפי הוראות סעיף קטן (א), זכאי לדרוש שהמידע האמור יימחק ולא יישמר; דרש מקבל פטור כאמור, ימחק עובד משרד הכלכלה או המפעיל את המידע.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;לא יגלה אדם מידע שהגיע אליו לפי סעיף קטן (א) ולא יעשה בו כל שימוש אלא לתכלית האמורה באותו סעיף קטן.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;מידע&quot; &ndash; מספר זהות, שם פרטי ושם משפחה, מען למשלוח דואר ומספר טלפון קווי ונייד;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;מפעיל&quot; &ndash; מי שמפעיל או מקיים מטעם משרד הכלכלה מרכז תעסוקה או פעילות להכוונה תעסוקתית.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-13" id="seif26-13">הכשרה תעסוקתית מיועדת (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יג. &nbsp;על אף האמור בסעיף 26כג, בתקופה שבה מיועד לשירות ביטחון שניתן לו צו דחיית שירות לפי פרק זה נמצא בהכשרה תעסוקתית מיועדת, יופחת מהיקף השעות כאמור בסעיף 26כג(א)(1), מספר השעות שבהן הוא נמצא בהכשרה תעסוקתית מיועדת, ולכל היותר 15 שעות בשבוע; בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;הכשרה תעסוקתית מיועדת&quot; &ndash; השכלה או הכשרה שאישר שר הכלכלה<sup>5</sup>, המיועדת לקידום שילובם המקצועי בתעסוקה של תלמידי ישיבה ששירותם נדחה לפי הוראות פרק זה, לרבות פעילות הכוונה מקצועית;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;השכלה&quot; &ndash; לימודי תכנית היסוד כמשמעותה בחוק חינוך ממלכתי, התשי&quot;ג-1953, כולה או חלקה, או השכלה אחרת שאישר שר הכלכלה על פי אמות מידה שקבע בשים לב לצורכי הצבא, ובלבד שהלימודים אינם לקראת תואר מוכר כמשמעותו בחוק המועצה להשכלה גבוהה, התשי&quot;ח-1958.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-14" id="seif26-14">דיווח לכנסת &ndash; שילוב בתעסוקה (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יד. &nbsp;שר הכלכלה<sup>5</sup> ידווח לוועדת הכלכלה של הכנסת, אחת לשנה, על השפעות יישום הוראות סימן זה על שילוב בוגרי ישיבות בתעסוקה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign4" id="sign4">סימן ד&apos;: יעדי גיוס</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-15" id="seif26-15">יעדי גיוס בתקופת ההסתכלות ובתקופת הקבע (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">טו. (א) &nbsp;בסימן זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;בוגר מוסד חינוך חרדי&quot; &ndash; מי שלמד בין גיל 14 לגיל 18, שנתיים לפחות, במוסד חינוך שהוא אחד מאלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; מוסד חינוך תרבותי ייחודי, כהגדרתו בחוק מוסדות תרבותיים ייחודיים, התשס&quot;ח-2008, המיועד לקבוצת האוכלוסייה המפורטת בפסקה (1) להגדרה &quot;קבוצה תרבותית ייחודית&quot; בחוק האמור;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מוסד לימודי חרדי או חלק ממוסד לימודי המיועד לתלמידים חרדים, שקבע, בצו, שר הביטחון, בהתייעצות עם שר החינוך או עם שר הכלכלה<sup>5</sup>, לפי העניין, ובאישור ועדת החוץ והביטחון של הכנסת;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;מתגייס בפועל&quot; &ndash; מי שהחל לשרת בשירות סדיר או בשירות לאומי-אזרחי, בשנת גיוס מסוימת;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;הצוות&quot; &ndash; הצוות הבין-משרדי שהוקם לפי הוראות סעיף 26טו1.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הממשלה תפעל, בתקופת ההסתגלות הראשונה ובתקופת ההסתגלות השנייה, להגדלה הדרגתית של מספר המתגייסים בפועל לשירות סדיר ולשירות לאומי-אזרחי (בסעיף זה &ndash; גיוס לשירות) מקרב תלמידי הישיבות ובוגרי מוסדות החינוך החרדים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; לשם הגדלה הדרגתית של מספר המתגייסים, תחליט הממשלה, בין השאר, על יעדים שנתיים של גיוס לשירות, בתקופת ההסתגלות הראשונה ובתקופת ההסתגלות השנייה, ועל אופן השגתם; היעדים השנתיים יכללו &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; יעד שנתי לגיוס לשירות סדיר שייקבע בשים לב להמלצת שר הביטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; יעד שנתי לגיוס לשירות לאומי-אזרחי, בחלוקה לשירות אזרחי-ביטחוני ושירות אזרחי-חברתי שייקבע בשים לב להמלצת השר הממונה על חוק שירות לאומי-אזרחי (בפרק זה &ndash; השר הממונה);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; יעד שנתי מצטבר, הכולל את היעדים השנתיים המפורטים בפסקאות (1) ו-(2) (בסעיף זה &ndash; יעד שנתי כולל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג1) בקביעת אופן השגתם של היעדים השנתיים כאמור בסעיף קטן (ג), תיתן הממשלה את דעתה לתכניות הרב-שנתיות שגיבש הצוות לפי סעיף 26טו2.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;בקביעת יעדי הגיוס תיתן הממשלה את דעתה גם למספר המיועדים לשירות ביטחון שניתן להם צו דחיית שירות לפי סעיף 26כ(ב) עד הגיעם לגיל 26, ולמספר יוצאי הצבא שניתן להם פטור לפי סעיף 26ה(ד) או 26ט(ו).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;יעדי הגיוס יעלו מדי שנת גיוס, ובלבד שהיעד השנתי הכולל לשנת הגיוס שתחילתה בחודש יולי 2013, לא יפחת מ-3,300 מתגייסים לשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; עלה על 1,000, בשנת גיוס מסוימת, מספר המתגייסים לשירות סדיר עד גיל 20 כלוחמים, יראו, לעניין יעדי הגיוס, כל שני מתגייסים נוספים עד גיל 20 כלוחמים, כשלושה; לעניין זה, &quot;לוחם&quot; &ndash; כהגדרתו בפקודות הצבא.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-15a" id="seif26-15a">הקמת צוות &nbsp;בין-משרדי לקידום עמידה ביעדי הגיוס (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">טו1. הממשלה תקים, בתוך 30 ימים מיום תחילתו של חוק שירות ביטחון (תיקון מס&apos; 21), התשע&quot;ו-2015 (בפרק זה &ndash; תיקון מס&apos; 21), צוות בין-משרדי, בראשות המנהל הכללי של משרד הביטחון, שחבריו יהיו:</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;ראש המועצה הלאומית לכלכלה הפועלת לפי החלטת הממשלה מספר 430 מיום י&quot;ז באלול התשס&quot;ו (10 בספטמבר 2006);</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;הממונה על התקציבים במשרד האוצר;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp;המנהל הכללי של משרד הכלכלה;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp;המנהל הכללי של משרד החינוך;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(5) &nbsp;המנהל הכללי של הרשות לשירות לאומי-אזרחי;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(6) &nbsp;נציג בכיר מטעם ראש המטה הכללי של צבא ההגנה לישראל;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(7) &nbsp;נציג היועץ המשפטי לממשלה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-15b" id="seif26-15b">תפקידי הצוות (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">טו2. (א) &nbsp; &nbsp; &nbsp; &nbsp;הצוות יגבש תכנית רב-שנתית לתקופת ההסתגלות הראשונה הכוללת אמצעים שיקדמו את העמידה ביעדי הגיוס לשירות סדיר ולשירות לאומי-אזרחי שנקבעו לפי סעיף 26טו; לשם כך רשאי הצוות, בין השאר, להציע תיקוני חקיקה ולהמליץ על שימוש בתכניות קיימות שהן בתחומי האחריות של משרדי הממשלה, לרבות תכניות המעוגנות בהחלטות הממשלה, בהוראות תקנות כספים ומשק (תכ&quot;מ) של החשב הכללי במשרד האוצר ובמבחני תמיכות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הצוות יגיש את התכנית כאמור בסעיף קטן (א), לשר הביטחון ולשר הממונה, לא יאוחר מתום 120 ימים מיום תחילתו של תיקון מס&apos; 21; שר הביטחון יגבש הצעת החלטה, בהסכמת השר הממונה, בהתאם לתכנית שהוגשה לו כאמור ויביאה לאישור הממשלה לא יאוחר מ-40 ימים מיום שהוגשה לו התכנית; לא הגיע שר הביטחון להסכמה עם השר הממונה בעניין הצעת ההחלטה כאמור, בתוך 30 ימים מיום שהוגשה לו התכנית, יגיש שר הביטחון את הצעת ההחלטה לממשלה ויביא בדברי ההסבר להצעה את עמדת השר הממונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שישה חודשים לפני תום תקופת ההסתגלות הראשונה, ישלים הצוות את גיבושה של תכנית רב-שנתית לתקופת ההסתגלות השנייה; על התכנית הרב-שנתית כאמור יחולו הוראות סעיף קטן (א), בשינויים המחויבים, והיא תגובש בהתחשב, בין השאר, במידת העמידה ביעדי הגיוס בתקופת ההסתגלות הראשונה וביעילותם של האמצעים שננקטו בהתאם לתכנית הרב-שנתית שגובשה לפי סעיף קטן (א).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;הצוות יגיש את התכנית כאמור בסעיף קטן (ג) לשר הביטחון ולשר הממונה, לא יאוחר משישה חודשים לפני תום תקופת ההסתגלות הראשונה; שר הביטחון יגבש הצעת החלטה, בהסכמת השר הממונה, בהתאם לתכנית שהוגשה לו כאמור ויביאה לאישור הממשלה לא יאוחר מ-40 ימים מיום שהוגשה לו התכנית; לא הגיע שר הביטחון להסכמה עם השר הממונה בעניין הצעת ההחלטה כאמור, בתוך 30 ימים מיום שהוגשה לו התכנית, יגיש שר הביטחון את הצעת ההחלטה לממשלה ויביא בדברי ההסבר להצעה את עמדת השר הממונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;הצוות יערוך מעקב מתמיד אחר יישום החלטות הממשלה שהתקבלו לפי סעיף 26טו(ג) במהלך תקופת ההסתגלות הראשונה ותקופת ההסתגלות השנייה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; הצוות ידווח לשר הביטחון ולשר הממונה, אחת לשנה, על ביצוע הוראות סעיף זה ובכלל זה על אמצעי המעקב שננקטו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-16" id="seif26-16">הודעות בדבר מספר המתגייסים לשירות סדיר והמשרתים בשירות לאומי-אזרחי (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">טז. (א) &nbsp;שר הביטחון יודיע לממשלה בתום כל שנת גיוס על מספר המתגייסים לשירות סדיר מקרב תלמידי הישיבות ובוגרי מוסדות החינוך החרדיים באותה שנת גיוס, ויפרסם על כך הודעה ברשומות לא יאוחר מ-5 ביולי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;השר הממונה יודיע לממשלה בתום כל שנת גיוס על מספר המשרתים בשירות לאומי-אזרחי שהחלו לשרת באותה שנת גיוס, ויפרסם על כך הודעה ברשומות לא יאוחר מ-5 ביולי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בלי לגרוע מהוראות סעיפים קטנים (א) ו-(ב), עד 5 בינואר בכל שנה תימסר לממשלה הודעה כמפורט להלן, והממשלה תפרסם על כך הודעה ברשומות לא יאוחר מ-15 בינואר:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הודעת שר הביטחון על מספר המתגייסים לשירות סדיר, לפי פרק זה, בששת החודשים הראשונים של שנת הגיוס;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הודעת השר הממונה על מספר המשרתים בשירות לאומי-אזרחי בששת החודשים הראשונים של שנת הגיוס.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign5" id="sign5">סימן ה&apos;: תקופת ההסתגלות השנייה</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-17" id="seif26-17">תחולה &ndash; תקופת ההסתגלות השנייה (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יז. &nbsp;הוראות סימן זה יחולו בתקופת ההסתגלות השנייה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-18" id="seif26-18">דחיית שירות למיועד לשירות ביטחון שהוא תלמיד ישיבה (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יח. (א) &nbsp;שר הביטחון, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה, רשאי לדחות, בצו, את מועד התייצבותו של המבקש לשירות סדיר, אם מצא כי מתקיימים לגביו התנאים לדחיית שירות, וראה לנכון לעשות כן בהתחשב בצורכי הביטחון ובהיקף הכוחות הסדירים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו דחיית שירות לפי סעיף זה יינתן לתקופה שלא תעלה על שנה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הביטחון רשאי במהלך תקופת ההסתגלות השנייה לחזור ולתת למיועד לשירות ביטחון כאמור בסעיף קטן (א) צו דחיית שירות לפי סעיף זה, עד הגיעו לגיל 21.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מלאו למיועד לשירות ביטחון 21 שנים במהלך שנת גיוס וביקש המיועד לשירות ביטחון לדחות את התייצבותו לשירות סדיר, רשאי שר הביטחון לדחות את מועד התייצבותו של המבקש עד תום שנת הגיוס, בכפוף להוראות סעיף קטן (א); לא ביקש המיועד לשירות ביטחון דלחות את מועד התייצבותו כאמור, יחולו עליו הוראות סעיף 26ז.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;בוטל צו דחיית שירות לפי סעיף זה בשל אי-עמידה בתנאים לדחיית השירות או פקע תוקפו של צו דחיית שירות לפי סעיף זה ולא ביקש מיועד לשירות ביטחון להאריכו, יחולו על המיועד לשירות ביטחון להאריכו, יחולו על המיועד לשירות ביטחון הוראות סעיף 13.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-19" id="seif26-19">תחולת סעיפים בשים לב לעמידה ביעדי הגיוס (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">יט. מיועד לשירות ביטחון ששירותו נדחה בתקופת ההסתגלות הראשונה בצו לפי סעיף 26יא וחלות עליו הוראות סעיף קטן (ד)(2) של הסעיף האמור, בין שניתן לגביו צו דחיית שירות נוסף לפי סעיף 26יח ובין שלא ניתן, ומלאו לו 21 שנים, יחולו עליו בשנת הגיוס שלאחר תום תקופת הצו האחרון לדחיית שירות שניתן לפי הסעיפים האמורים, הוראות אלה:</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;הושגו יעדי גיוס באותה שנת גיוס &ndash; יחולו עליו הוראות סעיף 26כ;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;לא הושגו יעדי גיוס באותה שנת גיוס &ndash; יחולו עליו הוראות סעיף 26ז לעניין קריאה לשירות סדיר או הפניה לשירות לאומי-אזרחי, בכפוף להוראות סעיף 26כא(ד) לגבי מועד הפעלת סמכויותיו של הפוקד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-20" id="seif26-20">הוראות לעניין עמידה ביעדי גיוס (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כ. &nbsp; (א) &nbsp;הושגו יעדי הגיוס שעליהם החליטה הממשלה בשנת גיוס מסוימת החל משנת הגיוס שתחילתה בחודש יולי 2019, רשאי שר הביטחון להמשיך ולדחות, בצו, את מועד התייצבותו לשירות סדיר של מיועד לשירות ביטחון שמתקיים בו האמור בסעיף 26יט רישה, באותה שנת גיוס, עד הגיעו לגיל 26, אף אם לא הושגו יעדי הגיוס בשנות הגיוס שלאחר אותה שנת גיוס, והכול במהלך תקופת ההסתגלות השנייה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מיועד לשירות ביטחון שהוא תלמיד ישיבה ששירותו נדחה לפי סעיף קטן (א), עד הגיעו לגיל 26, רשאי שר הביטחון לפטור אותו, בצו, מחובת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;text-indent:-51.05pt;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; (1) &nbsp; בוטל צו דחיית שירות שניתן לפי סעיף זה בשל אי-עמידה בתנאים לדחיית שירות, יחולו על המיועד לשירות ביטחון הוראות סעיף 13;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; פקע תוקפו של צו דחיית שירות שניתן לפי סעיף זה, ולא ביקש המיועד לשירות ביטחון להאריכו, יחולו עליו הוראות סעיף 26ז.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-21" id="seif26-21">הוראות לעניין &nbsp;אי-עמידה ביעדי גיוס (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כא. (א) &nbsp;לא הושג יעד גיוס שנתי שעליו החליטה הממשלה בשנת גיוס מסוימת, החל משנת הגיוס שתחילתה בחודש יולי 2019, לא יחולו הוראות סעיף 26כ מתחילת אותה שנת גיוס ואילך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מי שמתקיים בו האמור בסעיף 26יט רישה באותה שנת גיוס ואילך, יחולו עליו הוראות סעיף 26יט(2).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; על אף האמור בסעיף קטן (א), הוראות סעיף 26כ ימשיכו לחול על מי שהתקיים בו האמור בסעיף 26יט רישה שנים בשנים שקדמו לאותה שנת גיוס וניתן לו צו דחיית שירות לפי סעיף 26כ.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;על אף האמור בסעיף קטן (א), לא יפעיל הפוקד את סמכויותיו לפי סעיף 26ז עד ליום 1 בינואר שלאחר תום שנת הגיוס שבה לא הושג יעד שנתי אלא לגבי מי שבאותה תקופה פקע תוקפו של צו דחיית שירות שניתן לו ולא ביקש להאריכו; התקופה האמורה בסעיף קטן זה לא תבוא במניין החודשים לפי סעיף 20(ב).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-22" id="seif26-22">סמכויות שר הביטחון באין עמידה ביעדים (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כב. (א) &nbsp;על אף הוראות סעיף 26כא, לבקשת מיועד לשירות ביטחון שהוא תלמיד ישיבה שאינה ישיבה גבוהה ציונית, שפוקד רשאי לקרוא לו להתייצב לפי סעיף 26ז, רשאי שר הביטחון לדחות, בצו, את מועד התייצבותו לשירות סדיר, אם מצא כי מתקיימים לגביו התנאים לדחיית שירות וראה לנכון לעשות כן בהתחשב בצורכי הביטחון ובהיקף הכוחות הסדירים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו דחיית שירות לפי סעיף זה יינתן לתקופה שלא תעלה על שנה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הביטחון רשאי במהלך תקופת ההסתגלות השנייה לחזור ולתת למיוד לשירות ביטחון כאמור בסעיף קטן (א) צו דחיית שירות לפי סעיף זה, עד הגיעו לגיל 26.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הביטחון יחליט על מספר המיועדים לשירות ביטחון שיינתן להם צו דחיית שירות לפי סעיף זה בכל שנת גיוס, בשים לב ליעד השנתי הכולל לגיוס שקבעה הממשלה לפי סעיף 26טו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; נדחה שירותו של מיועד לשירות ביטחון שהוא תלמיד ישיבה לפי הוראות סעיף זה, מזמן לזמן, עד הגיעו לגיל 26, רשאי שר הביטחון לפטור אותו, בצו, מחובת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ז) &nbsp; פקע תוקפו של צו דחיית שירות שניתן לפי סעיף זה בטרם הגיע המיועד לשירות ביטחון לגיל 26, ולא הוארך, יחולו על המיועד לשירות ביטחון הוראות סעיף 26ז, ואם בוטל צו דחיית השירות בשל אי-עמידה בתנאים לדחיית שירות &ndash; יחולו הוראות סעיף 13.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:12.0pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:13px;font-family:Heebo-Regular;" name="sign6" id="sign6">סימן ו&apos;: הוראות כלליות לעניין דחיית שירות</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-23" id="seif26-23">תנאים לדחיית שירות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כג. (א) &nbsp;לא יינתן צו דחיית שירות לפי פרק זה אלא למיועד לשירות ביטחון שהוא תלמיד ישיבה שמתקיימים בו כל אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הוא לומד בישיבה לימודים תורניים, באופן סדיר, בהיקף שלא יפחת מ-45 שעות בשבוע, ובכולל &ndash; 40 שעות בשבוע, למעט בתקופות חופשה שקבע שר הביטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הוא לא עוסק בכל עיסוק נוסף על לימודיו בישיבה; ואולם בתקופת ההסתגלות הראשונה, ובתקופת ההסתגלות השנייה &ndash; לגבי מי שסעיף 26כ חל עליו, רשאי תלמיד ישיבה נשוי שמלאו לו 22 שנים, לעסוק בעיסוק נוסף על לימודיו בישיבה, ובלבד שלא יעסוק בעיסוק כאמר בשעות הלימודים בישיבה כאמור בפסקה (1); בפסקה זו, &quot;עיסוק&quot; &ndash; עבודה שמקובל לקבל בעבורה שכר, בין שהיא נעשית בשכר ובין שנעשית שלא בשכר, למעט הכשרה תעסוקתית מיועדת כהגדרתה בסעיף 26יג, ולרבות כל עיסוק אחר שקבע שר הביטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוא קיים את חובותיו לפי חוק זה, ובכלל זה התייצב לרישום ולבדיקה רפואית לפי סעיפים 3 ו-5;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; הוא הצהיר, בתצהיר בכתב שניתן לפי סעיף 15 לפקודת הראיות, כי מתקיימים בו התנאים שבפסקאות (1) עד (3);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(5) &nbsp; ראש הישיבה שבה הוא לומד אישר, בתצהיר בכתב שניתן לפי סעיף 15 לפקודת הראיות, כי מתקיים במיועד לשירות ביטחון התנאי שבפסקה (1) והתחייב כי אם יחדל להתקיים במיועד לשירות ביטחון התנאי האמור, יודיע על כך לפוקד בתוך 21 ימים;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(6) &nbsp; תנאים מוקדמים נוספים שקבע שר הביטחון, באישור ועדת החוץ והביטחון של הכנסת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ביקש מיועד לשירות ביטחון לחזור ולקבל צו דחיית שירות לפי פרק זה, יצרף מחדש לכל בקשה כאמור תצהירים והתחייבות כאמור בסעיף קטן (א)(4) ו-(5).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-24" id="seif26-24">רשימת הישיבות (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כד. (א) &nbsp;שר הביטחון יגבש את רשימת הישיבות לעניין פרק זה, בהתאם לאמות מידה שיקבע בתקנות; החלטת שר הביטחון בדבר הכללת ישיבה ברשימה תיעשה בהתחשב בהמלצת ועד הישיבות בארץ ישראל; אין בהוראות סעיף קטן זה כדי למנוע מישיבה לפנות לשר הביטחון בבקשה להיכלל ברשימת הישיבות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;רשימת הישיבות תפורסם באתר האינטרנט של אגף כוח אדם בצבא הגנה לישראל ותופקד לעיון הציבור הפוקד את לשכות הגיוס האזוריות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הביטחון רשאי להסיר ישיבה מרשימת הישיבות, לאחר שנתן לראש הישיבה הזדמנות לטעון את טענותיו לפניו, אם מצא כי התקיים אחד מאלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; שיעור מסוים מקרב תלמידי הישיבה שהם מיועדים לשירות ביטחון, אינו מתייצב לרישום ולבדיקה רפואית לפי סעיפים 3 ו-5; שר הביטחון יקבע את השיעור האמור;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; ראש הישיבה אינו ממלא את חובותיו בהתאם להוראות סעיף 26כג(א)(5);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; חדל להתקיים בישיבה אחד מהתנאים בהתאם לאמות המידה שקבע שר הביטחון כאמור בסעיף קטן (א);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; התקיימו נסיבות אחרות המצדיקות, לדעת שר הביטחון, את הסרתה מהרשימה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;שר הביטחון יקבע בתקנות הוראות לעניין תלמידי ישיבה שהוסרה מרשימת הישיבות ושעומדים בתנאים לדחיית שירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-25" id="seif26-25">מניעת פגיעה במעמדה של יוצא צבא אישה (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כה. לא ייפגע מעמדה ושילובה של יוצא צבא אישה בשירות ביטחון בשל שירותם של בוגרי ישיבות ומוסדות חינוך חרדיים בשירות ביטחון לפי פרק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-26" id="seif26-26">שמירה על אורח חיים (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כו. &nbsp;צבא הגנה לישראל יפעל לאפשר שמירה על אורח חייו של מי שהתגייס לשירות סדיר לפי פרק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-27" id="seif26-27">תקנות &ndash; פרק ג&apos;1 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כז. &nbsp;שר הביטחון, באישור ועדת החוץ והביטחון של הכנסת, רשאי להתקין תקנות לביצוע הוראות פרק זה, ובכלל זה תקנות בעניינים אלה:</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp;הדרכים והמועדים להגשת בקשות לפי פרק זה;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp;תנאים מקודמים להגשת בקשות לפי פרק זה;</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp;(נמחקה);</span></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:31.2pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp;התנאים והכללים להכללה ברשימת הישיבות לפי סעיף 26כד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-28" id="seif26-28">דיווח לכנסת &ndash; פרק ג&apos;1 (תיקון מס&apos; 19) תשע&quot;ד-2014 (תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כח. (א) &nbsp;שר הביטחון ידווח לוועדת החוץ והביטחון של הכנסת, אחת לשנה, לגבי שנת הגיוס שחלפה, בעניינים אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; מספר המתייצבים לשירות סדיר בכלל, ומספר המתייצבים כאמור מקרב בוגרי הישיבות ומוסדות החינוך החרדיים בפרט;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מספר צווי דחיית השירות וצווי הפטור משירות סדיר שניתנו לפי פרק זה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; נתונים בעניינים המפורטים בפסקאות (1) ו-(2) בהשוואה לשנים קודמות;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3א) ביצוע הוראות סעיף 26טו2, לרבות התכניות הרב-שנתיות שגיבש הצוות ואמצעי המעקב שננקטו לפי אותו סעיף, וכן החלטות הממשלה שהתקבלו בהתאם להוראות סעיף 26טו(ג);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; השפעת יישומן של הוראות פרק זה על השירות בשירות ביטחון, לרבות בכל הנוגע למעמדן ולשילובן של נשים בשירות ביטחון; דיווח לפי פסקה זו יימסר גם לוועדה לקידום מעמד האישה ולשוויון מגדרי של הכנסת;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(5) &nbsp; מסלולי שירות המותאמים לשירותם של בוגרי ישיבות ומוסדות חינוך חרדיים, שנפתחו או הורחבו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ועדת החוץ והביטחון של הכנסת תקיים אחת לשנה דיון בדיווחים שהתקבלו לפי סעיף קטן (א), לשם מעקב על ביצוע ההוראות לפי פרק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif26-29" id="seif26-29">הוראות לעניין פקיעת תוקפו של חוק שירות לאומי-אזרחי (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">26</span><span style="font-size:17px;font-family:Heebo-Regular;">כט. פקע תוקפו של חוק שירות לאומי-אזרחי לפי הוראות סעיף 36 לחוק האמור, לא יהיה רשאי עוד פוקד להפנות לשירות לאומי-אזרחי מיועד לשירות ביטחון שהפוקד קרא לו להתייצב לפי הוראות פרק זה.</span></p>
