            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">33</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">33</span><span style="font-size:17px;font-family:Heebo-Regular;">ד. (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med5" id="med5">פרק ה&apos;: הוראות כלליות</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">34. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif35" id="seif35">תחולת החוק הצבאי &nbsp;[27] (תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">35. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;יוצא-צבא החייב להתייצב לשירות סדיר, רואים אותו, לענין חוק השיפוט הצבאי, תשט&quot;ו-1955, כמי שנמנה עם הכוחות הסדירים של צבא-הגנה לישראל מן הזמן שנקבע בצו להתייצבות; לא התייצב, ללא צידוק מספיק, רואים אותו כאילו באותו זמן עזב את השירות שלא ברשות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif36" id="seif36">סמכות לפטור משירות ולדחות שירות [28]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">36. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">שר הבטחון רשאי בצו, אם ראה לעשות כן מטעמים הקשורים בהיקף הכוחות הסדירים או כוחות המילואים של צבא-הגנה לישראל, או מטעמים הקשורים בצרכי החינוך, ההתיישבות הבטחונית או המשק הלאומי, או מטעמי משפחה או מטעמים אחרים &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 14) תשס&quot;ז-2007</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לפטור יוצא-צבא או מיועד לשירות ביטחון שאינו יוצא-צבא מחובת שירות סדיר, או להפחית את תקופת שירותו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; לפטור יוצא-צבא מחובת שירות מילואים לתקופה מסויימת או לחלוטין;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; לפי בקשת יוצא-צבא או מיועד לשירות בטחון שאינו יוצא-צבא, לדחות בצו, לתקופה שיקבע בו, את מועד ההתייצבות שנקבע לאותו אדם לפי חוק זה לרישום, לבדיקה רפואית או לשירות בטחון או להמשך שירות זה אם כבר החל בשירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif36a" id="seif36a">פטור משירות מחמת גיל (תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">36</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;יוצא צבא שאינו קצין ושמלאו לו ארבעים שנים יהיה פטור מחובת שירות ביטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;יוצא צבא שהוא קצין ושמלאו לו ארבעים וחמש שנים יהיה פטור מחובת שירות ביטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; על אף הוראות סעיפים קטנים (א) ו-(ב) שר הביטחון רשאי לקבוע, בצו, באישור ועדת החוץ והביטחון של הכנסת, מקצועות או תפקידים שיוצא צבא הנמנה עמם יהיה זכאי לפטור מחובת שירות ביטחון רק אם מלאו לו ארבעים ותשע שנים; ואולם אם מלאו לו ארבעים שנים או ארבעים וחמש שנים, לפי העניין, לא ייקרא לשירות מילואים אלא בתפקידו או במקצועו; צו כאמור יינתן לתקופה שלא תעלה על שלוש שנים, וניתן להאריכו לתקופות נוספות שלא יעלו על שלוש שנים בכל פעם.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 17) תש&quot;ע-2010</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;לעניין סעיף זה, יראו יוצא צבא כמי שמלאו לו ארבעים שנים, ארבעים וחמש שנים או ארבעים ותשע שנים, לפי העניין, בתום השנה הקלנדרית שבה הגיע לגיל האמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif37" id="seif37">תנאים לדחיית שירות [29]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">37. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;דחיית שירות לפי סעיף 36(3) יכול שתהיה בתנאי או ללא תנאי; היתה הדחיה בתנאי, חייב מי שצו הדחיה חל עליו לקיים את התנאי, ואם עקב שינוי הנסיבות אין עוד בידו לקיימו, חייב הוא להתייצב, תוך שבעה ימים מהיום שבו חל השינוי, לפני הפוקד או לפני מי שהפוקד הסמיכו לכך ולהודיע לו על הנסיבות שבגללן אין עוד בידו לקיים את התנאי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הפוקד או מי שהוא הסמיכו לכך יתן למי שצו הדחיה חל עליו, לפי דרישתו, תעודה המעידה על התייצבותו כאמור; כל עוד לא בוטל הצו תשמש התעודה הצדק לאי-קיום התנאי.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; הוראות סעיף זה יחולו גם לגבי צו דחיית שירות שניתן לפי סעיף 22ב או פרק ג&apos;1, בשינויים המחויבים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif38" id="seif38">ביטול צו לפטור משירות או לדחיית שירות [29א] (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">38. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;נתן שר הבטחון צו לפי סעיף 36, סעיף 22ב או פרק ג&apos;1, רשאי הוא לבטל את הצו בהתקיים אחד מאלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הוא נתבקש בכתב לעשות כן מאת מי שהצו חל עליו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הצו ניתן בשל טעות, בפרט חשוב, של מי שהוסמך לתיתו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הטעמים שבגללם ניתן הצו אינם קיימים עוד;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; לא קויים תנאי מן התנאים שנקבעו בצו דחיה לפי סעיף 37(א).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ביטול לפי סעיף קטן (א) יהיה בצו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; שר הבטחון לא ישתמש בסמכותו לפי סעיף קטן (א)(2) עד (4) אלא לאחר שניתנה למי שהצו חל עליו הזדמנות לטעון נגד הביטול.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 3) תשמ&quot;ט-1989 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מי שחל עליו צו דחיה לפי סעיף 36(3), סעיף 22ב או פרק ג&apos;1 ולא נקרא להתייצב במועד אחר לרישום, לבדיקה רפואית, לשירות בטחון או להמשך השירות, חייב להתייצב למילוי חובתו בתום תקופת הדחיה, אולם שירותו יתחיל במועד שהורה לו הפוקד בצו; ואם בוטל הצו מכוח סעיף קטן (א) חייב הוא להתייצב למילוי החובה במועד שנקבע לכך בהודעה על ביטול הצו, ואם לא נקבע המועד &ndash; תוך ארבעה עשר ימים מהיום שבו הגיע צו הביטול לידיעתו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif39" id="seif39">פטור משירות על פי דין [30] (תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">39. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;אם לילד ואשה הרה יהיו פטורות מחובת שירות בטחון משהודיעו על כך; אופן ההודעה והראיות שיצורפו אליה ייקבעו בתקנות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;אשה נשואה פטורה מחובת שירות סדיר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; יוצא-צבא, אשה, שהוכיחה באופן שנקבע בתקנות לרשות שנקבעה על פיהן, כי טעמים שבמצפון או טעמים שבהווי משפחתי דתי מונעים אותה מלשרת בשירות בטחון, פטורה מחובת אותו שירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;יוצא-צבא, אשה, הרואה עצמה נפגעת על ידי החלטה של רשות כאמור בסעיף קטן (ג) רשאית, בתנאים ובדרך שייקבעו בתקנות, לערור עליה לפני ועדת ערר שימנה שר הבטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;ועדת הערר לא תהא קשורה בסדרי דין ובדיני הראיות אלא תפעל בדרך שתראה מועילה ביותר לבירור השאלות העומדות להחלטתה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif40" id="seif40">פטור מטעמי הכרה דתית [30א] (תיקון מס&apos; 18) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מיועדת לשירות בטחון שהצהירה בכתב לפי סעיף 15 לפקודת הראיות [נוסח חדש], תשל&quot;א-1971, בפני שופט או דיין בבית דין רבני, שלושה אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; טעמים שבהכרה דתית מונעים אותה מלשרת בשירות בטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; היא שומרת על כשרות בבית ומחוצה לו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; היא אינה נוסעת בשבת &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;תהא פטורה מחובת שירות בטחון לאחר שתמסור את התצהיר, באופן ובמועד שייקבעו בתקנות, לפוקד שהוסמך לכך.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 18) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;חדל להתקיים לגבי מיועדת לשירות ביטחון תנאי מהתנאים שלגביהם הצהירה כאמור בסעיף קטן (א), במהלך 24 החודשים שלאחר מסירת התצהיר או במהלך תקופת הקריאה להתייצבות לשירות סדיר כאמור בסעיף 20, לפי המאוחר, תודיע על כך מיד לפוקד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">ד. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">ה. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(הוראת שעה) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">40</span><span style="font-size:17px;font-family:Heebo-Regular;">ו. &nbsp; (פקע).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif41" id="seif41">ביטול פטור לפי סעיף 40(א) [32א] (תיקון מס&apos; 18) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">41. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מסרה מיועדת לשירות ביטחון הודעה לפי סעיף 40(ב) או הורשעה מיועדת לשירות בטחון בפסק דין סופי של בית משפט על עבירה לפי סעיף 46 או על עבירה לפי סעיף 239 לחוק העונשין, תשל&quot;ז-1977, בשל תצהיר לפי סעיף 40(א) &ndash; בטל מעיקרו הפטור לפי סעיף 40(א).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 18) תשע&quot;ב-2012</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הזמן שעבר מיום הפטור לפי סעיף 40(א) עד למועד מסירת הודעה לפי סעיף 40(ב) או עד ליום ההרשעה כאמור בסעיף קטן (א) לא יובא במנין התקופות שבסעיף 20.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif42" id="seif42">עיון חוזר [32]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">42. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;היה לפוקד יסוד סביר להניח, כי פטור או דחייה או קביעת אי-כושר כלשהי לגבי אדם פלוני היו תוצאה של מסירת ידיעות כוזבות או של מעשי מרמה או הטעיה אחרים, הן של אותו אדם והן של אחר, רשאי הוא להורות על עיון חוזר בדבר בידי מי שמוסמך לכך, לפי הענין.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;המקיים עיון חוזר יתן לנוגע בדבר הזדמנות להשמיע את דבריו, ואם שוכנע כי הפטור או הדחייה או קביעת אי-הכושר היו תוצאה ממסירת ידיעות או ממעשים כאמור, רשאי הוא לבטלם לפי חומר הראיות שלפניו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; לענין מילוי חובה לפי חוק זה, תקפה של החלטת ביטול בעיון חוזר היא למפרע מיום מתן הפטור או הדחייה או הקביעה הקודמת של אי-הכושר, והנוגע בדבר יקיים את הוראות חוק זה וכל תקנה, צו או הוראה אחרת שניתנו לפיו כאילו לא ניתנו הפטור או הדחייה או לא נקבע אי-כשרו לשירות בטחון, לפי הענין, ולא יבוא במנין התקופות שבסעיף 20 הזמן שעבר ממתן הפטור או הדחייה או מן הקביעה הקודמת עד לביטולם, לפי הענין.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;הוראות סעיף זה אינן גורעות לא מאחריותו הפלילית של אדם ולא מסמכויות אחרות, הן לפי חוק זה והן לפי כל דין אחר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;הוראות סעיף זה לא יחולו על פטור לפי סעיף 40.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif43" id="seif43">יציאה לחוץ לארץ (תיקון מס&apos; 4) תשנ&quot;א-1991 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">43. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מיועד לשירות בטחון ויוצא-צבא הנמנה עם הכוחות הסדירים של צבא-הגנה לישראל וכן מי שניתן לו צו דחיית שירות לפי סעיף 36, לפי סעיף 22ב או לפי פרק ג&apos;1 והצו עומד בתוקפו, לא יצאו לחוץ לארץ אלא לפי היתר מאת שר הבטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב1) &nbsp; &nbsp; &nbsp; &nbsp;היתר לפי סעיפים קטנים (א) ו-(ב) יכול שיהיה מותנה בתנאים, לרבות תנאי בדבר מועד חזרתו של מקבל ההיתר מחוץ לישראל, כפי שייקבע בהיתר, ויראו הפרת תנאי מתנאי ההיתר כאי מילוי חובה לפי חוק זה, לענין סעיף 46.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;(בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;שר הבטחון לאחר התייעצות עם שר החוץ רשאי לקבוע בתקנות מדינות שכניסת יוצאי צבא הנימנים עם כוחות המילואים לאותן המדינות תהיה אסורה, מוגבלת או מותנית בתנאים, ויראו הפרת הוראות אלו כאי מילוי חובה לפי חוק זה, לענין סעיף 46.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 12) &nbsp;תש&quot;ס-2000</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">43</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">43</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (בוטל).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif44" id="seif44">דרישת ידיעות [33]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">44. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון רשאי, בתקנות, לחייב בני אדם מסוגים שנקבעו בהן למסור לפוקד לפי דרישתו פרטים שנקבעו בתקנות, ככל שהם ידועים להם, בנוגע לאדם המיועד לשירות בטחון או שהוא יוצא-צבא שנמצא כשר לשירות, או בנוגע לסוג בני אדם כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;דרישה לפי סעיף קטן (א) למסור פרטים רפואיים לא תינתן אלא לרופא מורשה, לא תתייחס אלא לפרטים הדרושים לקביעת כושר לשירות, ולא תטיל חובה למסור פרטים כאמור אלא לפוקד שהוא חייל כמשמעותו בחוק השיפוט הצבאי, תשט&quot;ו-1955, שהוא רופא מורשה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; לא תידרש לפי סעיף זה מסירת פרטים על מחלת נפש אלא אם כן המחלה נקבעה בתקנות שהתקין שר הבטחון בהתייעצות עם שר הבריאות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;מי שהגיעה אליו לפי סעיף זה ידיעה בדבר מחלה, חייב לשמרה בסוד ולא לגלותה אלא במידה שיש בכך צורך לשם ביצוע חוק זה והתקנות לפיו, או אם הורשה לכך מאת בית משפט או מאת בית דין צבאי או מכוח החובה לענות על שאלות שמציג לו מי שמוסמך לערוך חקירות על ביצוע עבירות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;סעיף 28 לחוק לטיפול בחולי נפש, תשט&quot;ו-1955, לא יחול על מי שחייב למסור ידיעות לפי סעיף קטן (א).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif44a" id="seif44a">חסיון של נתון אישי צבאי (תיקון מס&apos; 6) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">44</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;לא ידרוש אדם ולא יעשה שימוש בכל נתון אישי צבאי, אלא אם כן הורשה לכך בחוק, בתקנות לפי חוק זה או בפקודות הצבא, ובלבד שהדרישה או השימוש ייעשו למטרה שלשמה הורשה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;שר הבטחון יקבע הוראות לענין מסירתו של מידע רפואי שבידי צבא הגנה לישראל, שאינו נתון אישי צבאי, ובלבד שאין איסור למסרו על פי דין.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בסעיף זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;דרישה&quot; &ndash; לרבות פניה, שמטרתה קבלת נתון אישי צבאי, על מנת לעשות בו שימוש לצרכי הפונה או אדם אחר;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;נתון אישי צבאי&quot; &ndash; הסימול המספרי המציין את מידת התאמתו של אדם לשירות הצבאי, או מסמך צבאי הקובע כושר לשירות צבאי או את מידת ההתאמה לשירות, וכן כל נתון אחר ששר הבטחון קבע בצו לענין זה;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;פקודות הצבא&quot; &ndash; כהגדרתן בסעיף 1 לחוק השיפוט הצבאי, תשט&quot;ו-1955.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif45" id="seif45">חובת שירות בטחון של השוהה בארץ ללא רשיון [34]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">45. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שהה אדם בישראל על פי רשיון לישיבת מעבר, לישיבת ביקור או לישיבת ארעי כאמור בחוק הכניסה לישראל, תשי&quot;ב-1952, ונשאר בישראל ששה חדשים לפחות לאחר שפקע תקפו של הרשיון בלי שחודש, רשאי פוקד להתרות בו בכתב כי אם לא ישיג רשיון לישיבה כאמור תוך שלושה חדשים מיום מסירת ההתראה לידיו, יראו, לענין שירות בטחון, את שהותו בישראל כישיבת קבע; עברו שלושה חדשים ולא הושג הרשיון, יראו, לענין חוק זה, מאותו יום ואילך את שהותו של האדם בישראל כישיבת קבע, וינהגו בו, לענין חוק זה, כאילו הגיע ארצה כעולה ביום האמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מסירתה של התראה לפי סעיף זה תהיה מסירה כדין, אף אם נמסרה לבן-משפחה של הנוגע בדבר הגר אתו ושמלאו לו שמונה עשרה שנים או אם נשלחה לנוגע בדבר בדואר רשום לפי מענו הרגיל או מענו האחרון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בחישוב כל תקופה לפי סעיף זה לא יבואו במנין &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; כל תקופה שבה היה הנוגע בדבר במאסר או במעצר לפי כל דין, או שהיה משוחרר ממאסר או ממעצר בערבות על פי כל דין;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; התקופה שבין הגשת הבקשה של הנוגע בדבר להיתר יציאה לפי כל דין לבין היום שבו הודיעו לו כדין על מתן ההיתר; הוראה זו לא תחול על איסור יציאה שהטיל בית המשפט או בית דין מוסמך בתובענה אזרחית, או שהטיל ראש ההוצאה לפועל בביצוע פסק דין או החלטה אחרת שניתנו בתובענה אזרחית.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med6" id="med6">פרק ו&apos;: עבירות וסדרי דין</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif46" id="seif46">עונשין [35(א) ו-(ב)]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">46. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;מי שעשה אחת מאלה, דינו &ndash; מאסר שנתיים:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לא מילא חובה המוטלת עליו בחוק זה או לפיו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; מסר ביודעין לרשות הפועלת לפי חוק זה ידיעה כוזבת בפרט שהוא חייב למסור לפי חוק זה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 4) תשנ&quot;א-1991 (תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; השיג צו של פטור משירות או של דחיית שירות לפי סעיף 36, פטור לפי סעיף 40, היתר יציאה לחוץ לארץ, תעודה או רישום לפי סעיף 43 או כל הקלה אחרת הניתנת לפי חוק זה, הכל על ידי כך שמסר ביודעין לרשות הפועלת לפי חוק זה ידיעה כוזבת בפרט חשוב;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 4) תשנ&quot;א-1991 (תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; עבר על הוראות סעיף 43.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מי שעשה אחת מאלה, דינו &ndash; מאסר חמש שנים:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; עשה אחד הדברים המפורטים בסעיף קטן (א), בכוונה להשתמט משירות בטחון;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; חיבל או הטיל מום בגופו, או הניח לאדם אחר לחבל או להטיל בו מום, בכוונה לפגוע על ידי כך בכשרו הרפואי לשירות בטחון.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 6) תשנ&quot;ה-1995</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; העובר על הוראות סעיף 44א, דינו &ndash; כפל הקנס הקבוע בסעיף 61(א)(1) לחוק העונשין, תשל&quot;ז-1977.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif47" id="seif47">עבירת חוץ [35(ג)]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">47. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">בתי המשפט בישראל מוסמכים לשפוט את מי שעבר בחוץ לארץ עבירה לפי סעיף 46.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif48" id="seif48">חובת הראיה [36]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">48. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">במשפט פלילי על אי מילוי חובת התייצבות לפי חוק זה &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הראיה על הנאשם, כי בזמן הנועד לקיום חובת ההתייצבות לא היה תושב קבוע ולא היה בגיל שבו הוא חייב בקיום החובה האמורה; אולם בית המשפט רשאי, אם ראה צורך בכך לשם עשיית צדק, להטיל את חובת הראיה בענינים האמורים על התובע;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; בכפוף לאמור בפסקה (1) אין על התובע להוכיח אלא שהנאשם חייב באותה חובה, ואם הוכיח זאת &ndash; על הנאשם הראיה שמילא אותה חובה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49" id="seif49">תקפו של צו [37]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">ניתן צו לפי חוק זה המטיל על אדם למלא חובה בזמן שנקבע בצו, והוא לא מילא את החובה באותו זמן, יעמוד הצו בתקפו לגבי אותו אדם עד שהוא ימלא את החובה; האמור בזה אינו גורע מאחריותו הפלילית של אדם על אי-מילוי חובה בזמן שנקבע בצו, וחיובו בדין על אי-מילוי חובה המוטלת עליו אינו פוטר אותו מאותה חובה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med6a" id="med6a">פרק ו&apos;1: פיקוח</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49a" id="seif49a">הסמכת מפקחים (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">א. &nbsp; (א) &nbsp;לשם פיקוח על הוראות חוק זה, רשאי שר הביטחון להסמיך, מבין עובדי משרדו או מבין עובדי המדינה שאינם עובדי משרדו &ndash; בהסכמת השר הנוגע בדבר, מפקחים שיהיו נתונות להם הסמכויות לפי סעיף 49ב, כולן או חלקן.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לא יוסמך מפקח, אלא אם כן מתקיימים בו כל אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הוא לא הורשע בעבירה שמפאת מהותה, חומרתה או נסיבותיה, אין הוא ראוי, לדעת השר, לשמש מפקח;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הוא קיבל הכשרה מתאימה בתחום הסמכויות שיהיו נתונות לו לפי פרק זה, כפי שהורה השר;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוא עומד בתנאי כשירות נוספים, כפי שהורה השר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49b" id="seif49b">סמכויות מפקח (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ב. &nbsp; (א) &nbsp;לשם פיקוח על ביצוע הוראות חוק זה, רשאי מפקח &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; לדרוש מכל אדם הרשום כתלמיד בישיבה או נמנה עם סגל הישיבה למסור לו את שמו ומענו ולהציג לפניו תעודת זהות או תעודה רשמית אחרת המזהה אותו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; לדרוש מכל אדם הנוגע בדבר למסור לו כי מידע או מסמך שיש בהם כדי להבטיח את ביצוע הוראות חוק זה; בפסקה זו, &quot;מסמך&quot; &ndash; לרבות פלט, כהגדרתו בחוק המחשבים, התשנ&quot;ה-1995;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; להיכנס לישיבה, ובלבד שלא ייכנס למקום המשמש למגורים, אלא על פי צו של בית משפט.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מצא מפקח כי מיועד לשירות ביטחון שהצהיר כי הוא לומד בישיבה אינו נוכח בה, יודיע על כך לפוקד או לאדם מטעמו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; מפקח לא יעשה שימוש בסמכויות הנתונות לו לפי סעיף זה, אלא בעת מילוי תפקידו, כשהוא עונד באופן גלוי תג המזהה אותו ואת תפקידו ויש בידו תעודה החתומה בידי שר הביטחון, המעידה על תפקידו ועל סמכויותיו שאותה יציג על פי דרישה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;בפרק זה, &quot;ישיבה&quot; &ndash; ישיבת הסדר, ישיבה גבוהה ציונית או ישיבה, כהגדרתן בסעיפים 22א, 22ב או 26ב, בהתאמה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49c" id="seif49c">הסמכת בודקים (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ג. &nbsp; (א) &nbsp;בלי לגרוע מהוראות סעיף 49א, שר הביטחון רשאי להסמיך בודקים, אף אם אינם עובדי המדינה, בסמכויות המפורטות בסעיף קטן (ב), ובלבד שלא יוסמך בודק כאמור, אלא אם כן התקיימו בו כל אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 21) תשע&quot;ו-2015</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הוא לא הורשע בעבירה שמפאת מהותה, חומרתה או נסיבותיה, אין הוא ראוי, לדעת השר, לשמש בודק;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הוא קיבל הכשרה מתאימה בתחום הסמכויות שיהיו נתונות לו לפי סעיף זה, כפי שהורה השר;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוא עומד בתנאי כשירות נוספים, כפי שהורה השר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לשם עריכת רישום נוכחות של מיועדים לשירות ביטחון שהם תלמידי ישיבה או של משרתים בשירות משולב כהגדרתו בסעיף 22א, רשאי בודק &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; להיכנס לחדרי לימוד בישיבה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; לדרוש מכל אדם הרשום כתלמיד ישיבה או נמנה עם סגל הישיבה הנמצא בחדרי לימוד בישיבה למסור לו את שמו ולהציג לפניו תעודת זהות או תעודה רשמית אחרת המזהה אותו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בודק לא יעשה שימוש בסמכויות הנתונות לו לפי סעיף זה, אלא בעת מילוי תפקידו כבודק וכשהוא עונד תג גלוי המזהה אותו כבודק.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49d" id="seif49d">העברת מידע לבודק (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ד. &nbsp; (א) &nbsp;לשם עריכת רישום נוכחות כאמור בסעיף 49ג, רשאי שר הביטחון או מי שהוא הסמיך לכך להעביר לבודק את שמו הפרטי, שם משפחתו ומספר זהותו של תלמיד ישיבה שניתן לו צו דחיית שירות, ואת שם הישיבה שבה הוא לומד; מידע כאמור יועבר רק לתכלית זו ובמידה שנדרש.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לא יועבר מידע לפי הוראות סעיף קטן (א) ולא ייעשה בו שימוש אלא בהתאם לנהלים שייקבעו במשרד הביטחון לעניין העברת המידע באופן מאובטח, שמירת המידע והבטחת מחיקתו; נהלים כאמור ייקבעו בהסכמת רשם מאגרי המידע כמשמעותו בסעיף 7 לחוק הגנת הפרטיות, התשמ&quot;א-1981 (בפרק זה &ndash; חוק הגנת הפרטיות).</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; בודק ישמור בסוד מידע שהועבר אליו לפי סעיף זה, לא יגלה אותו לאחר ולא יעשה בו כל שימוש, אלא לפי הוראות סעיף זה או כל דין אחר, או לפי צו של בית משפט.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49e" id="seif49e">חובות דיווח של ראש ישיבה (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ה. &nbsp; (א) &nbsp;ראש ישיבה ידווח לשר הביטחון, בתנאים ובמועדים שקבע השר, ככל שלא נקבעו בחוק זה, בעניינים אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; סדרי הלימוד בישיבה ושעות הלימוד השבועיות הקבועות שבהן מקיימים התלמידים את חובותיהם לפי חוק זה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; ימי חופשה, חג או היעדרות מותרת אחרת, שבהם לא מתקיימים לימודים בישיבה, והכול בהתאם לתנאים המותרים לפי חוק זה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הוספה או גריעה מרשימת תלמידי הישיבה שחלות עליהם הוראות סעיף 22א או 22ב או פרק ג&apos;1.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;שר הביטחון רשאי לקבוע חובות דיווח נוספות שיחולו על ישיבות או על ראשי ישיבות, הנחוצות לדעתו לשם פיקוח על התקיימות התנאים לדחיית שירות של תלמידי ישיבות, ובלבד שחובות דיווח העניין מידע כהגדרתו בסעיף 7 לחוק הגנת הפרטיות או ידיעה על ענייניו הפרטיים של אדם אף אם אינה בגדר מידע כאמור, ייקבעו בהסכמת שר המשפטים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49f" id="seif49f">מסירת מידע (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ו. &nbsp; (א) &nbsp;משרד החינוך ימסור למשרד הביטחון ולצבא הגנה לישראל, לפי דרישתם, נתונים שיש בידיו בדבר נוכחותם, בישיבה של תלמידי ישיבה, הדרושים למשרד הביטחון ולצבא הגנה לישראל לשם פיקוח על קיום הוראות חוק זה הנוגעות לתלמידי ישיבה או לישיבות; לשם כך, ימסרו משרד הביטחון וצבא הגנה לישראל למשרד החינוך, לפי דרישתו, את רשימת תלמידי הישיבה שקיבלו צו דחיית שירות לפי חוק זה או משרתים בשירות משולב כהגדרתו בסעיף 22א; מידע כאמור בסעיף קטן זה יימסר רק לתכלית האמורה ובמידה שנדרש.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;משרד החינוך ומשרד הביטחון יעבירו ביניהם נתונים על הלימודים בישיבות, לרבות נתונים על ביקורות שנערכו בישיבות לשם פיקוח, ובלבד שהנתונים לא יכללו מידע כהגדרתו בסעיף 7 לחוק הגנת הפרטיות, או ידיעה על ענייניו הפרטיים של אדם אף אם אינה בגדר מידע כאמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; אין בהוראות סעיף זה כדי למנוע מסירת מידע לפי הוראות פרק ד&apos; לחוק הגנת הפרטיות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49g" id="seif49g">הסתמכות על מידע ממשרד החינוך (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ז. &nbsp; לשם פיקוח על הוראות חוק זה הנוגעות לתלמידי ישיבה או לישיבות, רשאי שר הביטחון להסתמך על מידע שנמסר לו ממשרד החינוך כאמור בסעיף זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="seif49h" id="seif49h">דיווח לכנסת &ndash; פרק ו&apos;1 (תיקון מס&apos; 19) תשע&quot;ד-2014</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">49</span><span style="font-size:17px;font-family:Heebo-Regular;">ח. &nbsp;שר הביטחון ידווח לוועדת החוץ והביטחון של הכנסת, אחת לשנה, על פיקוח שנעשה לפי פרק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="med7" id="med7">פרק ז&apos;: דרכי ביצוע</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif50" id="#seif50">פוקדים [39]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">50. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון רשאי למנות פוקדים לענין חוק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;מינויו של פוקד יכול להיות כללי או מסוייג.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; הודעה על מינויו של פוקד תפורסם ברשומות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif51" id="#seif51">ועדות רפואיות [40]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">51. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">אופן הרכבתן של ועדה רפואית ושל ועדה רפואית עליונה וכן סדרי עבודתן ייקבעו בתקנות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif52" id="#seif52">סמכויות עזר [41]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">52. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;פוקד רשאי לאשר בתעודה בחתימת ידו כי אדם פלוני חייב במילוי חובה לפי חוק זה וכי לא קיים חובה זו בזמן שנקבע לקיומה; כל שוטר שבידו תעודה כאמור רשאי לעצור את האדם ולהחזיקו במעצר עד שיובא ללשכת הגיוס או למקום אחר הנועד להתייצבותו ועד שימלא את החובה המוטלת עליו, ובלבד שתקופת המעצר לפי סעיף זה לא תעלה על 48 שעות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;לענין סעיף 28(ב) לחוק ועדות חקירה, תשכ&quot;ט-1969, יהיו לפוקד, לועדה רפואית ולועדה רפואית עליונה, לרשות שנקבעה לפי סעיף 39(ג), לועדת ערר שמונתה לפי סעיף 39(ד) ולועדה מייעצת שנתמנתה לפי סעיף 53, בנוסף לסמכויותיהם האחרות לפי חוק זה או על פיו, כל הסמכויות לאסוף ראיות לשם הפעלת סמכויותיהם.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif53" id="#seif53">ועדות מייעצות [42]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">53. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">שר הבטחון רשאי למנות ועדות מייעצות לצורך ביצוע חוק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif54" id="#seif54">אצילת סמכויות [43]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">54. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;שר הבטחון רשאי לאצול לאדם אחר את הסמכויות הנתונות בידיו לפי חוק זה, כולן או מקצתן, חוץ מסמכויות אלה:</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; הסמכות להתקין תקנות;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; הסמכות למנות פוקדים לפי סעיף 50;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; הסמכות לתת צווים לפי סעיף 34(א)(1);</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; הסמכות למנות ועדות מייעצות לפי סעיף 53.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;הודעה על אצילת סמכויות לפי סעיף זה תפורסם ברשומות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif55" id="#seif55">דין צווים [44] (תיקון מס&apos; 4) תשנ&quot;א-1991 (תיקון מס&apos; 15) תשס&quot;ח-2008</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">55. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;צו לפי סעיפים 14, 24, 36 ו-38 יכול להיות אישי או לסוג מסויים; כל צו אחר לפי חוק זה יכול להיות כללי או לסוג מסויים או אישי; צווים אישיים לפי סעיף 38(א)(2),<br>&nbsp;(3) או (4) והוראה לפי סעיף 42 יהיו מנומקים, וצו אישי אחר ינומק אם דרש זאת האדם שעליו חל הצו, אולם חובת ההנמקה לא תחול אם בטחון המדינה מחייב שלא לגלות את נימוקי הצו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;צו לפי חוק זה אין חובה לפרסמו ברשומות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; צו יחייב את האדם שעליו הוא חל מן הזמן שהגיע לידיעתו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;צו שפורסם ברשומות, רואים אותו כאילו הגיע לידיעת האדם שעליו הוא חל בצהרי היום שלאחר יום הפרסום.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;צו שלא פורסם ברשומות, רואים אותו כאילו הגיע לידיעת האדם שעליו הוא חל &ndash;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(1) &nbsp; אם הוא נמסר לו או לאחד מבני משפחתו הגרים אתו ושאינו למטה מגיל שמונה עשרה &ndash; בזמן המסירה;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(2) &nbsp; אם הוא נשלח אליו בדואר רשום לפי מען מקום מגוריו הקבוע &ndash; בתום שבעים ושתיים שעות מזמן שנמסר לדואר למשלוח; לענין זה יראו כמען מקום מגוריו הקבוע של אדם את המען שמסר לפי חוק זה, ואם לא מסר &ndash; את מענו הרשום במרשם האוכלוסין;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(3) &nbsp; אם הוא פורסם ברדיו או בטלוויזיה או בלוחות המודעות של רשות מקומית &ndash; בתום ארבע שעות מזמן הפרסום;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(4) &nbsp; אם הוא פורסם בשני עתונים יומיים לפחות &ndash; בתום ארבע שעות מזמן הפצת העתונים במקום הימצאו;</span></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 9) &nbsp;תשנ&quot;ח-1997</span></strong></p>
            <p dir="RTL" style='margin-right:51.05pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">(5) &nbsp; אם הוא נשלח אליו בדואר לפי מען מקום מגוריו הקבוע, כמשמעותו בפסקה (2) &ndash; בתום שבעים ושתיים שעות מזמן שנמסר לדואר למשלוח, אם לא הוכח אחרת.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; צו שנמסר או נשלח לאדם שעליו הוא חל או שפורסם, הכל בדרך האמורה בסעיף קטן (ה), רואים אותו כאילו הגיע לידיעתו של אותו אדם בזמן האמור בסעיף קטן (ה), אף אם פורסם הצו לאחר מכן ברשומות.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;">(תיקון מס&apos; 3) תשמ&quot;ט-1989</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ז) &nbsp; צו אישי (בסעיף קטן זה &ndash; לרבות תעודה והיתר) לפי חוק זה יכול שייחתם בידי מי שהוסמך להוציאו או בידי חייל שהורשה לכך על ידיו ובלבד שצויינו בצו שמותיהם, דרגותיהם ותפקידיהם.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif56" id="#seif56">הגשת בקשות [45א]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">56. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">שר הבטחון רשאי בתקנות לקבוע מועד להגשת בקשות לפי חוק זה, אופן הגשתן ופרטים כיוצא באלה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="#seif57" id="#seif57">ביצוע ותקנות &nbsp;[38, 45]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">57. &nbsp;&nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">שר הבטחון ממונה על ביצוע חוק זה והוא רשאי להתקין תקנות בכל ענין הנוגע לביצועו, לרבות תקנות בדבר ביצוע חוק זה לגבי מיועד לשירות בטחון או לגבי יוצא-צבא שאינו מיועד לשירות בטחון השוהים בחוץ לארץ.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="Extras" id="Extras">תוספת</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span style="font-size:16px;font-family:Heebo-Regular;">(סעיף 2(3))</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras1" id="Extras1">הגדרות [1]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">1. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">בתוספת זו &ndash;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;תעודה אישית&quot; &ndash; תעודת לידה, דרכון, תעודת מסע, תעודת זיהוי ותעודה כיוצא באלה, וכן כל מסמך שבו רשם עובד הציבור, במילוי תפקידו, את גילו של אדם;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&quot;עובד הציבור&quot; &ndash; כמשמעותו בחוק העונשין, תשל&quot;ז-1977.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras2" id="Extras2">צו למסור ראיות בדבר גיל [2]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">2. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;כתאריך לידתו של אדם לצורך חוק זה ייחשב התאריך הרשום במרשם האוכלוסין, אם קיים רישום כזה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;ראה פוקד כי הרישום במרשם האוכלוסין אינו ברור דיו או כי אינו שלם, או כי אין במרשם האוכלוסין כל רישום בדבר תאריך לידתו של אדם פלוני, ויש לפוקד יסוד סביר להניח כי אותו אדם נקרא או עשוי להיקרא לרישום לפי סעיף 3 לחוק זה, רשאי הוא לצוות עליו, בצו אישי, למסור לפוקד ראיות בדבר גילו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras3" id="Extras3">סמכויות הפוקד לקביעת גיל [3]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">3. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">(א) &nbsp;פוקד רשאי לקבוע לפי חוק זה את גילו של אדם לפי ראיות שהובאו לפניו כאמור בסעיף 2(ב); לא הובאו ראיות כאמור או שלא שוכנע הפוקד בנכונותן של ראיות כאמור, או מצא הפוקד כי תאריך הלידה של אדם שונה מהתאריך הרשום במרשם האוכלוסין, יבקש מתן הצהרה על גילו בהתאם לחוק קביעת גיל, תשכ&quot;ד-1963.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ב) &nbsp;אדם שברישומי משם האוכלוסין לגביו לא נרשם החודש שבו נולד, או שנצטווה מכוח חוק זה למסור ראיות בדבר גילו, ולא הוכיח להנחת דעתו של פוקד את החודש שבו נולד, יורה הפוקד בכתב לנהוג כאילו נולד בחודש הראשון של השנה שבה נולד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ג) &nbsp; אדם שברישומי מרשם האוכלוסין לגביו לא נרשם היום בחודש שבו נולד, או שנצטווה מכוח חוק זה למסור ראיות בדבר גילו, ולא הוכיח להנחת דעתו של פוקד את היום שבו נולד, יורה הפוקד בכתב לנהוג בו כאילו נולד ביום הראשון של החודש שבו נולד.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ד) &nbsp;נקבע לאדם מתח גילים לפי חוק קביעת גיל, תשכ&quot;ד-1963, רשאי הפוקד להורות בכתב לנהוג בו כאילו נולד באמצע התקופה של מתח הגילים.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ה) &nbsp;רשאי פוקד, בכל מקרה שיש לו יסוד סביר להניח כי אדם נקרא או עשוי להיקרא לרישום לפי סעיף 3 לחוק זה, לצוות עליו, בצו אישי, להתייצב לפני ועדה רפואית לשם בדיקת גילו מבחינת התפתחותו הגופנית, השכלית והנפשית, בזמן ובמקום שיקבעו הוא או הועדה הרפואית, ואותו אדם חייב להתייצב לפני הועדה הרפואית בזמן ובמקום האמורים, להיחקר בידיה ולהיבדק כל בדיקה רפואית או אחרת שהועדה תמצא לנחוצה לשם קביעת גילו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(ו) &nbsp; &quot;שנה&quot; ו&quot;חודש&quot;, לענין סעיף זה &ndash; השנה והחודש לפי הלוח ששימש להוכחת שנת לידתו של האדם.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras4" id="Extras4">שינוי קביעת גיל לפי בקשת הפוקד [4]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">4. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">נקבע גילו של אדם או שונתה הקביעה לפי חוק קביעת גיל, תשכ&quot;ד-1963, רשאי הפוקד, אם לא הוזמן להופיע בבירור הבקשה כאמור בסעיף 7 לאותו חוק, לבקש מבית משפט השלום שינוי הקביעה, ורשאי הפוקד לעשות כן אף אם אין בידו ראיות נוספות על אלו שהיו בפני בית המשפט; בבירור בקשה זו רשאי הפוקד לחקור את האדם ועדים שהעידו בבירור בקשה קודמת לקביעת גילו או לשינויה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras5" id="Extras5">סתירה בין תעודות אישיות להודעות [5]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">5. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">הודיע אדם פלוני, או מי שאדם פלוני עומד בפיקוחו, לעובד הציבור הפועל במילוי תפקידו על גילו של אותו אדם, והודעה זו אינה מתיישבת עם הרישום בתעודות אישיות של אותו אדם, רשאי פוקד להורות בכתב, לאחר שנתן הזדמנות לנוגע בדבר להשמיע את טענותיו ולאחר ששוכנע כי ההודעה לא ניתנה לצרכי השתמטות משירות בטחון ואף אין טעם סביר אחר לסתור את ההודעה, כי לענין חוק זה יראו את ההודעה כנכונה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras6" id="Extras6">שמירת סמכויות בית המשפט [10]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">6. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">הוראות תוספת זו אינן גורעות מסמכויות בית המשפט לפי כל דין אחר.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:right;line-height:normal;'><strong><span style="font-size:18px;font-family:Heebo-Regular;color:green;" name="Extras7" id="Extras7">מסירת הודעות [11]</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:21px;font-family:Heebo-Regular;">7. &nbsp; &nbsp;</span><span style="font-size:17px;font-family:Heebo-Regular;">הפוקד יביא כל הוראה לפי תוספת זו לידיעת הנוגע בדבר, בדרך שבה מותר להביא צו לידיעתו של אדם לפי סעיף 55(ה) לחוק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:center;line-height:normal;'><strong><span style="font-size:16px;font-family:Heebo-Regular;" name="compare1" id="compare1">לוח השוואה לפי הסעיף הקודם</span></strong></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-size:17px;font-family:Heebo-Regular;">&nbsp;</span></p>
