            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ג מס&apos; 1888</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 31.3.2003 עמ&apos; 377 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ג מס&apos; 21</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 246) &ndash; הוראת שעה תשס&quot;ג-2003 (שירות מילואים); תוקפה עד יום 31.12.2003.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ד מס&apos; 1925</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 9.2.2004 עמ&apos; 294 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ג מס&apos; 32</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 486) &ndash; תיקון מס&apos; 13; תחילתו ביום 1.1.2004 ור&apos; סעיף 3 לענין תחולה.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">3. (א) תחילתו של חוק זה ביום ז&apos; בטבת התשס&quot;ד (1 בינואר 2004) (בסעיף זה &ndash; יום התחילה).</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">(ב) מי שערב יום התחילה חלפה לגביו התקופה לקריאה לשירות, לפי הוראות בעיפים 2 ו-20 לחוק העיקרי, כנוסחן ערב יום התחילה, ימשיכו לחול עליו הוראות סעיפים אלה, כנוסחן ערב יום התחילה; לענין סעיף קטן זה, &quot;התקופה לקריאה לשירות&quot; &ndash; כהגדרתה בסעיף 20(ג2) לחוק העיקרי, כנוסחו ערב יום התחילה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ה מס&apos; 1981</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 14.2.2005 עמ&apos; 138 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ה מס&apos; 160</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 560) &ndash; הוראת שעה תשס&quot;ה-2005 (יוצאי צבא בשירות בתי הסוהר); תוקפה עד יום 31.12.2021. תוקנה</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ח מס&apos; 2150</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 10.4.2008 עמ&apos; 496 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ח מס&apos; 371</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 430) &ndash; הוראת שעה (תיקון); תחילתו ביום 14.2.2008.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ח מס&apos; 2176</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 6.8.2008 עמ&apos; 814 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ח מס&apos; 394</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 636) &ndash; הוראת שעה (תיקון מס&apos; 2); תחילתה ביום 1.7.2008.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ט מס&apos; 2207</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 5.8.2009 עמ&apos; 304 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ט מס&apos; 428</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 315) &ndash; הוראת שעה (תיקון מס&apos; 3) תשס&quot;ט-2009; תחילתו ביום 24.5.2009.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תש&quot;ע מס&apos; 2256</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 29.7.2010 עמ&apos; 653 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תש&quot;ע מס&apos; 506</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 1056) &ndash; הוראת שעה (תיקון מס&apos; 4) תש&quot;ע-2010; תחילתו ביום 25.5.2010.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;א מס&apos; 2273</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 26.1.2011 עמ&apos; 204 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;א מס&apos; 553</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 337) &ndash; הוראת שעה (תיקון מס&apos; 5) תשע&quot;א-2011; תחילתו ביום 1.1.2011.</span></p>
