            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ו מס&apos; 2532</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 17.2.2016 עמ&apos; 576 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ו מס&apos; 993</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 328) &ndash; הוראת שעה (תיקון מס&apos; 7).</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;background:#F3F3F3;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ט מס&apos; 2768</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 1.1.2019 עמ&apos; 108 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ט מס&apos; 1283</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 256) &ndash; הוראת שעה (תיקון מס&apos; 8); תחילתה ביום 1.1.2019.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ז מס&apos; 2111</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 11.9.2007 עמ&apos; 472 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הכנסת תשס&quot;ו מס&apos; 104</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 62) &ndash; תיקון מס&apos; 14.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשס&quot;ח מס&apos; 2152</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 16.4.2008 עמ&apos; 511 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ז מס&apos; 295</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 584) &ndash; תיקון מס&apos; 15 בסעיף 32 לחוק שירות המילואים, התשס&quot;ח-2008; ר&apos; סעיף 49(א), (ב) לענין תחילה.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">49. (א) תחילתו של חוק זה, למעט הסעיפים המפורטים בסעיף קטן (ב), ביום כ&quot;ט בתמוז התשס&quot;ח (1 באוגוסט 2008) (בסעיף זה &ndash; יום התחילה).</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">(ב) תחילתו של סעיף 7, וכן תחילתו של ביטול כותרת פרק ד&apos; לחוק שירות ביטחון וסעיפים 28, 29 ו-32 באותו פרק, לפי הוראות סעיף 32(4) לחוק זה, ביום ט&quot;ו בטבת התש&quot;ע (1 בינואר 2010); הוועדה תקיים דיון בהוראות הסעיפים האמורים בסמוך למועד האמור.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תש&quot;ע מס&apos; 2229</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 16.2.2010 עמ&apos; 383 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשס&quot;ט מס&apos; 458</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 26) &ndash; תיקון מס&apos; 16 בסעיף 18 לחוק שירות הקבע בצבא הגנה לישראל (הליכים לעניין החלטות הנוגעות לחיילים בשירות קבע), תש&quot;ע-2010; תחילתו שישה חודשים מיום פרסומו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תש&quot;ע מס&apos; 2234</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 17.3.2010 עמ&apos; 420 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תש&quot;ע מס&apos; 468</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 112) &ndash; תיקון מס&apos; 17.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ב מס&apos; 2345</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 19.3.2012 עמ&apos; 218 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תש&quot;ע מס&apos; 472</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 150) &ndash; תיקון מס&apos; 18 והוראת שעה; ר&apos; סעיף 4 לענין תחילתה של הוראת השעה, תוקפה לשלוש שנים.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">4. תחילתו של סעיף 3 ביום פרסומן של תקנות לפי הוראות סעיפים 40ג לחוק העיקרי, כנוסחם בסעיף 3 לחוק זה; תקנות ראשונות כאמור יובאו לאישור הוועדה המשותפת לוועדת החוץ והביטחון ולוועדת החוקה חוק ומשפט של הכנסת בתוך שלושים ימים מיום פרסומו של חוק זה.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ד מס&apos; 2441</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 19.3.2014 עמ&apos; 350 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ג מס&apos; 787</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 1124) &ndash; תיקון מס&apos; 19; ר&apos; סעיפים 15-13, 18 לענין תחילה, תחולה והוראות מעבר. תוקן&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ה מס&apos; 2493</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 30.6.2015 עמ&apos; 192 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ה מס&apos; 917</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 598) &ndash; תיקון מס&apos; 19 (תיקון) תשע&quot;ה-2015.&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ו מס&apos; 2512</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 30.11.2015 עמ&apos; 266 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ו מס&apos; 973</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 186) &ndash; תיקון מס&apos; 19 (תיקון מס&apos; 2) תשע&quot;ו-2015 בסעיף 18 לתיקון מס&apos; 21.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">13. בכפוף להוראות סעיפים 14 ו-15, תחילתו של חוק זה ביום י&quot;ח באדר ב&apos; התשע&quot;ד (20 במרס 2014).</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">14. תחילתו של סעיף 15 לחוק העיקרי, כנוסחו בסעיף 1 לחוק זה, ביום י&quot;ד בתמוז התשע&quot;ה (1 ביולי 2015).</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">15. (א) תחילתו של סעיף 16(1) לחוק העיקרי, כנוסחו בסעיף 2 לחוק זה, ביום כניסתן לתוקף של תקנות לפי סעיף 16א(ד) לחוק העיקרי, כנוסחו בסעיף 3 לחוק זה, ובלבד שלא יובאו לאישור ועדת החוץ והביטחון של הכנסת לפני יום כ&quot;ה בסיוון התשע&quot;ו (1 ביולי 2016); ואולם לא הובאו תקנות כאמור לאישור ועדת החוץ והביטחון של הכנסת עד יום כ&quot;ז בסיוון התשע&quot;ט (30 ביוני 2019), יפקע תוקפם של סעיפים 16(1) ו-16א(ד) לחוק העיקרי, כנוסחם בסעיפים 2 ו-3 לחוק זה.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">&nbsp;(ב) פורסמו תקנות כאמור בסעיף קטן (א), יבחן שר הביטחון את הצורך בתיקון תקנות שירות ביטחון (קביעת תפקידים לשירות נשים בהתנדבות), התשס&quot;א-2001 (להלן &ndash; תקנות שירות נשים בהתנדבות), כך שבתוך שנתיים מיום פרסומן של התקנות כאמור בסעיף קטן (א), יהיה משך השירות בתפקידים המנויים בתקנות שירות נשים בהתנדבות כאמור בסעיף 16 לחוק העיקרי, כנוסחו בסעיף 2 לחוק זה.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:2.0pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">18. (א) מי שערב יום התחילה שירת בשירות אזרחי לפי חוק דחיית שירות לתלמידי ישיבות שתורתם אומנותם, התשס&quot;ב-2002 (בסעיף זה &ndash; חוק דחיית שירות) או בשירות אזרחי משמר לפי החלטת הממשלה מס&apos; 5275 מיום כ&quot;ה בכסלו התשע&quot;ג (9 בדצמבר 2012), יראו אותו כמי שהופנה לשירות לאומי-אזרחי לפי סעיף 26ז(ג) לחוק העיקרי, כנוסחו בסעיף 6 לחוק זה, ולא יחולו לגביו הוראות סעיף 26ט לחוק העיקרי, כנוסחו בסעיף 6 לחוק זה, ואולם לגבי שירותו כאמור יחולו ההוראות לפי חוק דחיית שירות שחלו על שירות אזרחי ערב פקיעת החוק האמור.</span></p>
            <p dir="RTL" style='margin-right:8.5pt;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><span style="font-family:Heebo-Regular;color:black;">&nbsp;(ב) ישיבה שהיתה כלולה ברשימה שקבע שר הביטחון לפי סעיף 8 לחוק דחיית שירות ערב פקיעתו של החוק האמור, יראו אותה כישיבה שנקבעה לפי סעיפים 22א, 22ד או 26כד לחוק העיקרי, כנוסחם בסעיפים 5 ו-6 לחוק זה, לפי העניין, <s>עד תום שנה <u>שנתיים</u> מיום תחילתו של חוק זה</s> <u>כל עוד לא הוסרה מהרשימה לפי הוראות אותם סעיפים</u>.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ה מס&apos; 2473</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 16.11.2014 עמ&apos; 18 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ב מס&apos; 622</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 42) &ndash; תיקון מס&apos; 20.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ו מס&apos; 2512</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 30.11.2015 עמ&apos; 262 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ו מס&apos; 973</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 186) &ndash; תיקון מס&apos; 21.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ז מס&apos; 2592</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 29.12.2016 עמ&apos; 232 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הממשלה תשע&quot;ז מס&apos; 1083</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 433) &ndash; תיקון מס&apos; 22 בסעיף 35 לחוק ההתייעלות הכלכלית (תיקוני חקיקה ליישום המדיניות הכלכלית לשנות התקציב 2017 ו-2018), תשע&quot;ז-2016; תחילתו ביום 1.7.2020.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><span style="font-family:Heebo-Regular;color:blue;">ס&quot;ח תשע&quot;ט מס&apos; 2775</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 7.1.2019 עמ&apos; 158 (</span><u><span style="font-family:Heebo-Regular;color:blue;">ה&quot;ח הכנסת תשע&quot;ח מס&apos; 766</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;עמ&apos; 124) &ndash; תיקון מס&apos; 23; תחילתו שנה מיום פרסומו.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><sup><span dir="LTR" style="font-size:13px;font-family:Heebo-Regular;color:blue;">[1]</span></sup></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;הסמכת ראש המחלקה באגף משאבי אנוש של משמר הגבול:&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">י&quot;פ תשע&quot;ה מס&apos; 7065</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 28.6.2015 עמ&apos; 6896.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><sup><span dir="LTR" style="font-size:13px;font-family:Heebo-Regular;color:blue;">[2]</span></sup></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;הפרק בוטל בג&quot;ץ 1877/14&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">התנועה למען איכות השלטון בישראל נ&apos; הכנסת</span></u><span style="font-family:Heebo-Regular;color:black;">; תוקף הביטול מיום 1.2.2021.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><sup><span dir="LTR" style="font-size:13px;font-family:Heebo-Regular;color:blue;">[3]</span></sup></u><span style="font-size:13px;font-family:Heebo-Regular;color:black;">&nbsp;</span><span style="font-family:Heebo-Regular;color:black;">סמכויותיו הועברו לשר הרווחה והשירותים החברתיים:&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">י&quot;פ תשע&quot;ז מס&apos; 7394</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 7.12.2016 עמ&apos; 1314.</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:2.0cm;font-size:15px;font-family:Heebo-Regular;margin-top:3.6pt;margin-bottom:.0001pt;text-align:justify;line-height:normal;'><u><sup><span dir="LTR" style="font-size:13px;font-family:Heebo-Regular;color:blue;">[4]</span></sup></u><span style="font-size:13px;font-family:Heebo-Regular;color:black;">&nbsp;</span><span style="font-family:Heebo-Regular;color:black;">ר&apos;&nbsp;</span><u><span style="font-family:Heebo-Regular;color:blue;">ק&quot;ת תשס&quot;ו מס&apos; 6472</span></u><span style="font-family:Heebo-Regular;color:black;">&nbsp;מיום 3.4.2006 עמ&apos; 646; תוקפו עד יום 31.12.2006.</span></p>
            <p dir="LTR" style='margin-right:0cm;margin-left:0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:.0001pt;text-align:center;line-height:normal;'><span dir="RTL" style="font-size:18px;font-family:Heebo-Regular;color:black;">&nbsp;</span></p>
            <p dir="RTL" style='margin-right:0cm;margin-left:0cm;font-size:15px;font-family:Heebo-Regular;margin-top:0cm;margin-bottom:10.0pt;text-align:right;line-height:115%;'><span dir="LTR" style="font-family:Heebo-Regular;">&nbsp;</span></p>
        </div>
    </div>
</div>
</template>

